import React, { Component } from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import Search from '../../../utils/commonStyles/timeSearch/timeSearch'
import PictureArea from './pictureArea'
import ChartArea from './chartArea'
import {relTime} from '../../../utils/util'
import TableList from './tableList'
import pagination from '../../../utils/page'
import styles from './index.module.less'
import moment from "moment"
class Category extends Component {
  static getDerivedStateFromProps(props){
    const data = []
    const trend=[]
    if(props.category.data){
      data.push(props.category.data)
      trend.push(props.category.trend)
      return{
        data,
        trend:Array.isArray(props.category.trend)?props.category.trend:trend
      }
    }
  }
  constructor (props) {
    super(props)
    this.state = {
      data:[],
      trend:[],
      order:'descend',
      sort :'pv',
    }
  }
  componentDidMount(){
    const {order, sort,data} = this.state
    this.setState({
      trend:data
    })
    this.props.seatch({
      page:pagination.current,
      length:pagination.pageSize,
      order,
      sort,
      date:[moment().format('YYYY-MM-DD'),moment().format('YYYY-MM-DD')],
    })
  }
  onChange=(pagination, filters, sorter)=>{
    this.props.seatch({
      page:pagination.current,
      length:pagination.pageSize,
      order:sorter&& sorter.order,
      sort:sorter && sorter.field,
    })
  }
  
  chagetrend = (value)=>{
    this.props.changeTrend(value)
  }
  changeType = (value)=>{
    this.props.seatch({period:value})
  }
  setch=(value)=>{
    this.props.seatch({date:relTime(value)})
  }
  render () {
    return (
      <div className={styles.webComponent}>
        <Search title={'频道访问'} simpleDate RangeDate Period onChangeType={this.changeType}  onChange={this.setch}/>
        <div className='web-main'>
          <PictureArea info ={this.props.category.data}/>
          <ChartArea data={this.state.trend} chagetrend={this.chagetrend}/>
          <TableList onChange={this.onChange} data={this.state.data}/>
        </div>
      </div>

    )
  }
}

export default withRouter(connect(({ category }) => ({
  category
}), ({ category: { seatch,changeTrend} }) => ({
  seatch,
  changeTrend
}))(Category))
