import React, { Component } from 'react'
import { HashRouter, Route, Switch, Redirect } from 'react-router-dom'
import { Header, Sider } from './commonComponents'
import Dashboard from './dashboard'
import styles from './index.module.less'

import Overview from './overview'
import Web from './web'
import Category from './category'
import Page from './page'
import Area from './area'
import User from './user'
import Publish from './publish'
import Article from './article'

class MainLayout extends Component {
  constructor (props) {
    super(props)
    this.state = {
      recordDomSize: {
        width: null,
        height: null
      }
    }
  }

  render () {
    if (this.props.location.pathname.indexOf('/main/dashboard') === -1) {
      window.document.body.style.background = '#0E1F36'
    } else {
      window.document.body.style.background = '#02265f'
    }
    return <div className={this.props.location.pathname.indexOf('/main/dashboard') === -1 ? styles.mainComponentBgc : styles.mainBgc}>
      <Header {...this.props} />
      <div className={this.props.location.pathname.indexOf('/main/dashboard') === -1 ? styles.componentContainer : styles.dashboardContainer}>
        {this.props.location.pathname.indexOf('/main/page') >= 0 && <div style={{ display: 'inline-block', width: '256px', background: '#19345F', flex: '0 0 256px' }}><Sider {...this.props} /></div>}
        <div className={this.props.location.pathname.indexOf('/main/dashboard') === -1 ? styles.componentPage : styles.dashboardPage}>
          <HashRouter>
            <Switch>
              <Route exact path='/main/dashboard' component={Dashboard} />
              <Route exact path='/main/page/overview' component={Overview} />
              <Route exact path='/main/page/web' component={Web} />
              <Route exact path='/main/page/category' component={Category} />
              <Route exact path='/main/page/page' component={Page} />
              <Route exact path='/main/page/area' component={Area} />
              <Route exact path='/main/page/user' component={User} />
              <Route exact path='/main/page/publish' component={Publish} />
              <Route exact path='/main/page/article' component={Article} />
              <Redirect to='/main/dashboard' />
            </Switch>
          </HashRouter>
        </div>
      </div>

    </div>
  }
}

export default MainLayout
