import React, { Component } from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { Table } from 'antd'
import styles from './index.module.less'

const { Column } = Table

const columns = [
  {
    title: '频道',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: '文章数',
    dataIndex: 'articleCount',
    key: 'articleCount',
  },
  {
    title: '浏览量(PV)',
    dataIndex: 'pv',
    key: 'pv',
  },
  {
    title: 'ip数',
    dataIndex: 'ipTotal',
    key: 'ipTotal',
  },
  {
    title: '访客(UV)',
    dataIndex: 'uvTotal',
    key: 'uvTotal',
  },
];

class TableList extends Component {
  constructor (props) {
    super(props)
    this.state = {
      loading: false,
    }
  }


  render () {
    const {data}= this.props
    return <div className={styles.table}>
      <Table
        size='small'
        pagination={false}
        bordered
        columns={columns}
        // columns={this.getColumn()}
        // loading={this.state.loading}
        dataSource={data}>
      </Table>
    </div>
  }
}

export default withRouter(connect(({ category }) => ({
  category
}), ({ category: { searchDataByTime } }) => ({
  searchDataByTime
}))(TableList))
