import moment from 'moment'
import * as apis from './server'

export default {
  state: {
    loading: false,
    period: 'hour',
    mode: ['date', 'date'],
    total: 0,
    data:[],
    date:[moment().format('YYYY-MM-DD'),moment().format('YYYY-MM-DD')],
    length:60,
    page:1,
    sort:'pv',
    order:'descend',
    period:'hour',
    info:null
  },
  reducers: {
    setPageLoading (state, payload) {
      state.loading = payload
      return state
    },
    setTime (state, payload) {
      return {
        ...state,
        ...payload
      }
    },
    setData(state,payload){
      state.data = payload.visits || []
      state.total = payload.total ||0
    },
    setInfo(state,payload){
      state.info = payload.summary
    },
    setState(state,payload){
      for(let i in payload){
        state[i]=payload[i]
      }
    },
  },
  effects: dispatch => ({
    async queryInfo (payload) {
      const data = await apis.visitSummary(payload)
      this.setInfo(data)
    },
    async seatch (payload,state) {
      this.setPageLoading(true)
      const {page, length, order, sort,date,period} = payload
      if(page){
        this.setState({page})
      }
      if(length){
        this.setState({length})
      }
      if(sort){
        this.setState({sort})
      }
      if(order){
        this.setState({order})
      }
      if(date){
        this.setState({date})
      }
      if(period){
        this.setState({period})
      }
      this.queryInfo({date:date?date:state.web.date})
      const data = await apis.visitList({
        page:page,
        order:order?order:state.web.order,
        length:length?length:state.web.length,
        sort:sort?sort:state.web.sort,
        date:date?date:state.web.date,
        period:period?period:state.web.period,
      })
      this.setData(data)
      this.setPageLoading(false)
    },
  })
}
