import React, { Component } from 'react'
import echarts from 'echarts'
import china from './data/china_geo';
// import {
//   BarOption,
//   PieOption,
//   LineOption,
//   MapOption
// } from './chart_option'

/**
 * domName 容器 class 名字
 * type 类型
 * option echarts 配置项
 */

class Chart extends Component {
  componentDidMount () {
    const { domName, type, option } = this.props
    let myChart = echarts.init(document.getElementsByClassName(domName)[0])
    if ((type === 'bar') || (type == 'pie') || (type == 'line')) {
      myChart.setOption(option)
      if(this.props.onClick){
        myChart.on('click', this.props.onClick)
      }
    } else if (type === 'map') {
      // 地图的不太一样要单独处理
      this.createMap(this.mapNode)
    }
  }

  componentWillReceiveProps (nextProp) {
    if (nextProp.timestamp !== this.props.timestamp){
      const { domName, type, option } = nextProp
      let myChart = echarts.init(document.getElementsByClassName(domName)[0])
      if ((type === 'bar') || (type == 'pie') || (type == 'line')) {
        myChart.setOption(option, true)
        if(this.props.onClick){
          myChart.on('click', this.props.onClick)
        }
      } else if (type === 'map') {
        // 地图的不太一样要单独处理
        setTimeout(()=>{
          this.createMap(this.mapNode)
        },200)
      }
    }
  }
  
  createMap = (element) => {
    const { option } = this.props
    echarts.registerMap('china', china);
    const myChart = element && echarts.init(element);
    element &&myChart.setOption(option, true);
  };

  setMapElement = n => {
    this.mapNode = n;
  }

  render () {
    const { domName } = this.props
    return <div className={domName}  style={{ width: '100%', height: '100%' }} ref={this.setMapElement}/>
  }
}

export default Chart
