import React, { Component } from 'react'
import { Row, Col } from 'antd'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import CurrentVisitor from './currentVisitor'
import WebVisitor from './webVisitor'
import TopTenCategory from './topTenCategory'
import PageVisitor from './pageVisitor'
import Area from './area'
import styles from './index.module.less'

class Overview extends Component {
  componentWillMount () {
    const { getOtherCharts } = this.props
    getOtherCharts()
  }

  render () {
    // const { overflow: { visits, channels, pages, map } } = this.props
    return (
      <div className={styles.overview}>
        <Row>
          <Col span={24}>
            <CurrentVisitor />
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <WebVisitor {...this.props} />
          </Col>
          <Col span={12}>
            <TopTenCategory {...this.props} />
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <PageVisitor {...this.props} />
          </Col>
          <Col span={12}>
            <Area {...this.props} />
          </Col>
        </Row>
      </div>
    )
  }
}

export default withRouter(connect(({ overflow }) => ({
  overflow
}), ({ overflow: { getOtherCharts } }) => ({
  getOtherCharts
}))(Overview))
