import colors from '../../../../utils/charts/color'

function produceOption (data) {
  if (data) {
    for (var i = 0; i < data.length; i++) {
      data[i].value = data[i].pv
    }
  } else {
    data = []
  }

  let option = {
    color: colors,
    legend: {
      orient: 'vertical',
      left: 0,
      top: '10%',
      selectedMode: false,
      itemWidth: 8,
      itemHeight: 8,
      itemGap: 10,
      textStyle: {
        color: '#fff'
      }
    },
    series: [
      {
        type: 'pie',
        silent: true,
        label: {
          show: false
        },
        labelLine: {
          show: false
        },
        radius: ['70%', '80%'], // 可视容器的min(宽,高)
        center: ['60%', '50%'],
        data: data
      }
    ]
  }
  return option
}

export default produceOption
