import colors from '../../../../utils/charts/color'

function produceOption (data) {
  let dataX = []
  let dataY = []
  data && data.map((item) => {
    dataX.push(item.label)
    dataY.push(item.pv)
  })

  let option = {
    color: colors,
    xAxis: {
      type: 'category',
      data: dataX,
      axisLabel: {
        color: '#fff'
      }
    },
    yAxis: {
      type: 'value',
      axisLabel: {
        color: '#fff'
      }
    },
    grid: {
      top: '15%',
      left: '15%',
      width: '80%',
      height: '70%'
    },
    series: [
      {
        type: 'line',
        data: dataY
      }
    ]
  }
  return option
}

export default produceOption
