// import { message } from 'antd'
import moment from 'moment'
import * as apis from "./server"

export default {
  state: {
    loading: false,
    period: 'hour',
    mode: ['date', 'date'],
    total: 0,
    data:[],
    date:[moment().format('YYYY-MM-DD'),moment().format('YYYY-MM-DD')],
    length:60,
    page:1,
    sort:'pv',
    order:'descend',
    search:'',
    trendData:[],
  },
  reducers: {
    setPageLoading (state, payload) {
      state.loading = payload
      return state
    },
    setData(state,payload){
      state.data = payload.articles || []
      state.total = payload.total || 0
    },
    setTrendData(state,payload){
      state.trendData = payload.trends || []
      state.circle = payload.circle || []
    },
    setState(state,payload){
      for(let i in payload){
        state[i]=payload[i]
      }
    },
  },
  effects: dispatch => ({
    async pageTrend (payload,state) {
      const data = await apis.pageTrend({
        date:payload.date,
      })
      this.setTrendData(data)
    },
    async seatch (payload,state) {
      this.setPageLoading(true)
      const {page, length, order, sort,date, search} = payload
      if(!isNaN(page)){
        this.setState({page})
      }
      if(length){
        this.setState({length})
      }
      if(sort){
        this.setState({sort})
      }
      if(order){
        this.setState({order})
      }
      if(date){
        this.setState({date})
      }
      this.pageTrend({
        date:date?date:state.article.date,
      })
      const data = await apis.pageList({
        page:page,
        order:order?order:state.article.order,
        length:length?length:state.article.length,
        sort:sort?sort:state.article.sort,
        date:date?date:state.article.date,
        search:search?search:state.article.search,
      })
      this.setData(data)
      this.setPageLoading(false)
    },
  })
}
