import colors from '../../../../../utils/charts/color'

function produceOption (data,lable) {
  const option = {
    color: colors,
    legend: {
      icon: 'circle',
      data: [data.name],
      textStyle: {
        color: '#647897'
      },
      bottom: 0
    },
    grid: {
      height: '70%',
      width: '80%',
      left: '10%',
      top: '20px'
    },
    xAxis: {
      type: 'category',
      axisLabel: {
        color: '#647897',
        interval:0,
        rotate:40
      },
      axisLine: false,
      data: (function () {
        var res = []
        data.map((item) => {
          res.push(item.name)
        })
        return res
      })()
    },
    yAxis: {
      type: 'value',
      axisLabel: {
        color: '#647897'
      },
      axisLine: false,
      splitLine: {
        show: true,
        lineStyle: {
          color: ['#647897'],
          width: 1,
          type: 'solid'
        }
      }
    },
    series: [
      {
        type: 'bar',
        data: (function () {
          var res = []
          data.map((item) => {
            res.push(item[lable])
          })
          return res
        })()
      }
    ]
  }

  return option
}

export default produceOption
