import React, { Component } from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import Search from '../../../utils/commonStyles/timeSearch/timeSearch'
import ChartArea from './chartArea'
import {relTime} from '../../../utils/util'
import TableList from './tableList'
import pagination from '../../../utils/page'
import moment from 'moment'
import styles from './index.module.less'

class Page extends Component {
  constructor (props) {
    super(props)
    this.state = {
      data:[],
      order:'descend',
      sort :'pv',
      trendData:{}
    }
  }
  componentDidMount(){
    const {order, sort} = this.state
    this.props.seatch({
      page:pagination.current,
      length:pagination.pageSize,
      order,
      sort,
      date:[moment().format('YYYY-MM-DD'),moment().format('YYYY-MM-DD')],
    })
  }

  setch=(value)=>{
    this.props.seatch({date:relTime(value)})
  }
  onSearch=(value)=>{
    this.props.seatch({search:value})
  }
  onChange=(pagination, filters, sorter,prop)=>{
    this.props.seatch({
      page:pagination.current,
      length:pagination.pageSize,
      order:sorter&& sorter.order,
      sort:sorter && sorter.field,
      search:prop.search
    })
  }
  render () {
    console.log(this.state.trendData)
    return (
      <div className={styles.webComponent}>
        <Search title={'文章统计'} simpleDate RangeDate onChange={this.setch}/>
        <div className='web-main'>
          <ChartArea data={this.props.article.trendData} info={this.props.article.circle}/>
          <TableList total={this.props.article.total} onChange={this.onChange} data={this.props.article.data} onSearch={this.onSearch}/>
        </div>
      </div>

    )
  }
}

export default withRouter(connect(({ article }) => ({
  article
}), ({ article: {seatch, pageTrend} }) => ({
  seatch,
  pageTrend
}))(Page))
