import React, { Component } from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import DimensionsDropdown from '../../../../utils/commonStyles/labelSelect'
import styles from './index.module.less'
import option from './chartOption'
import Chart from '../../../../utils/charts/chart'
import moment from 'moment'
import { Icon, Spin } from 'antd'

const goals = [
  { label: '浏览量(PV)', value: 'pv' },
  { label: '访客数(UV)', value: 'uv' },
  { label: 'IP数', value: 'ip_num' },
  { label: '跳出率', value: 'bounce_rate' },
  { label: '平均访问时长', value: 'ave_interview_duration' }
]

class ChartArea extends Component {
  static getDerivedStateFromProps(props){
    return{
      timestamp: moment(new Date()).valueOf()
    }
  }
  constructor(props){
    super(props)
    this.state = {
      dimensions: ['pv'],
      status: true,
      lable:'pv',
      timestamp: moment(new Date()).valueOf()
    }
  }

  onDimensionsChange = (data) => {
    this.setState({
        dimensions: [data],
        lable:data,
       
    },()=>{
      this.setState({
        timestamp: moment(new Date()).valueOf()
      })
    });
  }

  changeLabel = () => {
    this.setState({
      status: !this.state.status
    })
  }

  render () {
    const { data,info, page: { chartsData, loadingList } } = this.props
    const { lable } = this.state


    return <div style={{clear:'both',marginTop: '120px'}}>
      <div style={this.state.status ? {display: 'block'} : {display: 'none'}} className={styles.chartArea}>
        <DimensionsDropdown
          options={goals}
          checkCount={1}
          defaultValue={['pv']}
          onChange={this.onDimensionsChange}
          getPopupContainer={node => {
            return node.parentNode.parentNode
          }} />
        <div className='chart-main'>
          {loadingList ? <Spin /> : <Chart domName={'selectChartOption'} type={'line'} option={option(data, info, lable)} timestamp={this.state.timestamp}/>}
        </div>
      </div>
      <div className={styles.chartArea} style={!this.state.status ? {display: 'block'} : {display: 'none'}}>
        <div style={{color: '#B7D5FE'}}>图形报表</div>
      </div>

      <div className={styles.close} onClick={this.changeLabel}>
        {this.state.status ? <Icon type="double-left" style={{transform:'rotate(90deg)'}}/>
        : <Icon type="double-left" style={{transform:'rotate(270deg)'}}/>}
      </div>
    </div>
  }
}

export default withRouter(connect(({ page }) => ({
  page
}))(ChartArea))