import moment from 'moment'
import * as apis from './server'

export default {
  state: {
    loading: false,
    period: 'hour',
    mode: ['date', 'date'],
    total: 0,
    data:[],
    date:[moment().format('YYYY-MM-DD'),moment().format('YYYY-MM-DD')],
    length:60,
    page:1,
    sort:'pv',
    order:'descend',
    period:'hour',
    trend:[],
  },
  reducers: {
    setPageLoading (state, payload) {
      state.loading = payload
      return state
    },
    setTime (state, payload) {
      return {
        ...state,
        ...payload
      }
    },
    setData(state,payload){
      state.data =  payload.channels || {}
      // state.data = []
      state.total = payload.total
    },
    setTrend(state,payload){
      state.trend = payload
    },
    setState(state,payload){
      for(let i in payload){
        state[i]=payload[i]
      }
    },
  },
  effects: dispatch => ({
    changeTrend (payload){
      this.setTrend(payload)
    },
    async seatch (payload,state) {
      this.setPageLoading(true)
      const {page, length, order, sort,date, period} = payload
      if(page){
        this.setState({page})
      }
      if(length){
        this.setState({length})
      }
      if(sort){
        this.setState({sort})
      }
      if(order){
        this.setState({order})
      }
      if(date){
        this.setState({date})
      }
      if(period){
        this.setState({period})
      }
      const data = await apis.channelList({
        // page:page?page:state.category.page,
        // order:order?order:state.category.order,
        // length:length?length:state.category.length,
        // sort:sort?sort:state.category.sort,
        // period:period?period:state.category.period,
        date:date?date:state.category.date,
      })
      this.setData(data)
      this.changeTrend (data.channels || {})
      this.setPageLoading(false)
    },
  })
}
