import colors from '../../../../../utils/charts/color'

function produceOption (data) {
  // console.log(1233333333333, data)
  let dataName = ['浏览量(PV)', '访客量(UV)']
  let dataX = []
  let dataY1 = []
  let dataY2 = []

  data.pv && data.pv.map((item) => {
    dataX.push(item.label)
    dataY1.push(item.pv)
  })

  data.uv && data.uv.map((item) => {
    dataY2.push(item.uv)
  })

  let option = {
    color: colors,
    title: {
      text: '近1小时访问趋势',
      textStyle: {
        color: '#fff'
      },
      x: 'center',
      align: 'right'
    },
    tooltip: {
      trigger: 'axis'
    },
    legend: {
      data: dataName,
      bottom: 40,
      icon: 'circle',
      textStyle: {
        color: '#647897'
      }
    },
    xAxis: {
      type: 'category',
      axisLine: false,
      data: dataX,
      axisLabel: {
        color: '#fff'
      }
    },
    yAxis: {
      type: 'value',
      axisLine: {
        show: false
      },
      axisLabel: {
        color: '#647897'
      },
      splitLine: {
        show: true,
        lineStyle: {
          color: ['#647897'],
          width: 1,
          type: 'solid'
        }
      }
    },
    grid: {
      top: '50',
      height: '60%',
      color: '#647897'
    },
    series: [
      {
        name: dataName[0],
        type: 'line',
        data: dataY1
      },
      {
        name: dataName[1],
        type: 'line',
        data: dataY2
      }
    ]
  }
  // console.log('option', option)
  return option
}

export default produceOption
