import React, { Component } from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import Search from '../../../utils/commonStyles/timeSearch/timeSearch'
import PictureArea from './pictureArea'
import ChartArea from './chartArea'
import TableList from './tableList'
import {relTime} from '../../../utils/util'
import pagination from '../../../utils/page'
import styles from './index.module.less'
import moment from 'moment'

class Web extends Component {
  constructor (props) {
    super(props)
    this.state = {
      data:[],
      order:'descend',
      sort :'pv',
    }
  }
  componentDidMount(){
    const {order, sort} = this.state
    this.props.seatch({
      ...pagination,
      page:pagination.current,
      length:pagination.pageSize,
      order,
      sort,
      date:[moment().format('YYYY-MM-DD'),moment().format('YYYY-MM-DD')],
    })
  }
  onChange=(pagination, filters, sorter)=>{
    this.props.seatch({
      page:pagination.current,
      length:pagination.pageSize,
      order:sorter&& sorter.order,
      sort:sorter && sorter.field,
    })
  }
  changeType = (value)=>{
    console.log(value)
    this.props.seatch({period:value})
  }
  setch=(value)=>{
    this.props.seatch({date:relTime(value)})
  }
  render () {
    return (
      <div className={styles.webComponent}>
        <Search title={'网站访问'} simpleDate RangeDate Period onChangeType={this.changeType}  onChange={this.setch}/>
        <div className='web-main'>
          <PictureArea info ={this.props.web.info}/>
          <ChartArea data={this.props.web.data}/>
          <TableList onChange={this.onChange} total={this.props.web.total} data={this.props.web.data}/>
        </div>
      </div>

    )
  }
}

export default withRouter(connect(({ web }) => ({
  web
}), ({ web: { seatch,} }) => ({
  seatch,
}))(Web))
