class CurrentVisitor extends Component {
  constructor (props) {
    super(props)
    this.state = {
      minNum: 10
    }
  }

  componentDidMount(){
    // window.addEventListener()
    const { getPVandUV } = this.props
    getPVandUV()
    this.calculator()
  }

  componentWillUnmount(){
    clearTimeout(this.timer)
    this.setState = (state, callback) => {
      return
    }
  }

  calculator = () => {
    const { getPVandUV } = this.props
    this.timer = setInterval(()=>{
      if(this.state.minNum === 1){
        getPVandUV()
        this.setState({
          minNum: 10
        })
      }else{
        this.setState({
          minNum: this.state.minNum - 1
        })
      }

    },60*1000*10)
  }

  render () {
    const { overflow: { pvAndUvForTenMinutes, timestamp } } = this.props

    let pvNum = (pvAndUvForTenMinutes && pvAndUvForTenMinutes.pv) ? pvAndUvForTenMinutes.pv[pvAndUvForTenMinutes.pv.length-1].pv : 0
    let uvNum = (pvAndUvForTenMinutes && pvAndUvForTenMinutes.uv) ? pvAndUvForTenMinutes.uv[pvAndUvForTenMinutes.uv.length-1].uv : 0
    // console.log('pvAndUvForTenMinutes', pvAndUvForTenMinutes)
    return (
      <div className={styles.currentVisitor}>
        <div className='current-visitor'>
          <div className='header'>
              实时访客
          </div>
          <div className='main'>
            <div className='left-data'>
              <div className='current-text'>
                实时浏览量（近10分钟）
                {/* <div className='num-data'>1234</div> */}
                <div className='num-data'>{pvNum}</div>
              </div>
              <div className='current-text'>
                实时访客量（近10分钟）
                <div className='num-data'>{uvNum}</div>
              </div>
              <div className='refresh-time'>
                <span>{this.state.minNum} </span>分钟后刷新数据
              </div>
            </div>
            <div className='right-chart'>
              {<Chart domName={'currentVisitor'} type={'line'} option={lineOption(pvAndUvForTenMinutes)} timestamp={timestamp}/>}
            </div>
          </div>
        </div>
      </div>
    )
  }
}

// export default CurrentVisitor

export default withRouter(connect(({ overflow }) => ({
  overflow
}), ({ overflow: { getPVandUV } }) => ({
  getPVandUV
}))(CurrentVisitor))