/* eslint-disable no-param-reassign */
import { list, summary } from './service'
import pagination from '../../../utils/page'
import { message } from 'antd'
import moment from "moment"
import * as apis from './service'

export default {
  state: {
    loading: false,
    period: 'hour',
    mode: ['date', 'date'],
    total: 0,
    data:[],
    date:[moment().format('YYYY-MM-DD'),moment().format('YYYY-MM-DD')],
    length:60,
    page:1,
    sort:'pv',
    order:'descend',
    trend:[],
    circle:[],
  },
  reducers: {
    setPageLoading (state, payload) {
      state.loadingList = payload
      return state
    },
    uploadState (state, payload) {
      return {
        ...state,
        ...payload
      }
    },
    setData(state,payload){
      state.data = payload.pages?payload.pages:[]
      state.total = payload.total ||0
    },
    setInfo(state,payload){
      state.info = payload.summary
    },
    setTrend(state,payload){
      state.trend = payload.trends ||[]
      state.circle = payload.circle ||[]
    },
    setState(state,payload){
      for(let i in payload){
        state[i]=payload[i]
      }
    },
  },
  effects: dispatch => ({
    async queryInfo (payload) {
      const data = await apis.visitSummary(payload)
      this.setInfo(data)
    },
    async queryTrend (payload) {
      const data = await apis.visitTrend(payload)
      this.setTrend(data)
    },
    // 查询列表
    async seatch (payload,state) {
      this.setPageLoading(true)
      const {page, length, order, sort,date, search} = payload
      if(page){
        this.setState({page})
      }
      if(length){
        this.setState({length})
      }
      if(sort){
        this.setState({sort})
      }
      if(order){
        this.setState({order})
      }
      if(date){
        this.setState({date})
        this.queryTrend({
          date:date?date:state.page.date,
        })
      }
      this.queryInfo({date:date?date:state.page.date})
      const data = await apis.list({
        page:page,
        order:order?order:state.page.order,
        length:length?length:state.page.length,
        sort:sort?sort:state.page.sort,
        date:date?date:state.page.date,
        search:search?search:state.page.search,
      })
      this.setData(data)
      this.setPageLoading(false)
    },
  })
}
