
import React, {Component} from 'react';
import {DatePicker, message} from 'antd';
import moment from 'moment';

const {RangePicker} = DatePicker;

class RangeDatePicker extends Component {

    static defaultProps = {
        date: [moment(), moment()],
        mode: ['date', 'date']
    };

    constructor(props) {
        super(props);
        this.onChange = this.onChange.bind(this);
        this.onDisabledDate = this.onDisabledDate.bind(this);
    }

    onChange(dates) {
        const { currentPeriod } = this.props
        let timeDiff = moment(dates[1]).diff(moment(dates[0]), 'days')
        if(currentPeriod === 'day' && timeDiff > 60){
            message.error('按日查询时间不能超过 60 天')
            return
        }else if(currentPeriod === 'month' && timeDiff > 365){
            message.error('按月查询时间不能超过 365 天')
            return
        }
        this.props.onChange({componentName: 'RangeDatePicker', value: dates});
    }

    onChangeForHour = (dates) => {
        this.props.onChange({componentName: 'RangeDatePicker', value: [dates, dates]});
    }

    onDisabledDate(current) {
        // console.log('currentPeriod', current, moment().startOf('day'))
        // if(currentPeriod === 'day'){
        //     return current && current > moment().startOf('day') && current < moment().startOf('day').add(60, 'days')
        //     moment().subtract(6, 'days')
        // }
        return current && current > moment().startOf('day');
    }
    disabledDate=(current)=>{
        // Can not select days before today and today
        return current && current > moment().endOf('day')|| current < moment().startOf('day')
    }
    disabledToday=(current)=>{
        // Can not select days before today and today
        return current > moment().startOf('day')
    }

    render() {
        const { currentPeriod, Period, slot} = this.props
        const date = this.props.date.slice();
        const format = this.props.mode[0] == 'date' ? 'YYYY/MM/DD' : 'YYYY/MM';
        const value = this.props.mode[0] == 'date' ? date : [date[0].startOf('month'), date[1].endOf('month')]
        // console.log('value',value, this.props.mode)
        return (
            <div style={{paddingTop: '1px'}}>
                {(Period && currentPeriod === 'hour') ? <DatePicker
                    value={value[0]}
                    onChange={this.onChangeForHour}
                    allowClear={false}
                    disabledDate={slot && slot=='today'?this.disabledToday:this.onDisabledDate} /> 
                : <RangePicker
                    format={format}
                    value={value}
                    onChange={this.onChange} size="small"
                    mode={this.props.mode}
                    onPanelChange={this.onChange}
                    allowClear={false}
                    disabledDate={slot && slot=='today'?this.disabledToday:this.onDisabledDate}
                />}
            </div>
        );
    }
}

export default RangeDatePicker;