
import React, { Component } from 'react'
// import { Spin } from 'antd'
import styles from './index.module.less'

class TopList extends Component {
  render () {
    const { list } = this.props

    // console.log('this.props.colNames', this.props.colNames[2])
    return (
      // <Spin spinning={this.props.loading}>
      <ul className={styles.topList}>
        <li className='head'>
          <span className='col-1'>{this.props.colNames[0]}</span>
          <span className='col-2'>{this.props.colNames[1]}</span>
          <span className='col-3'>{this.props.colNames[2]}</span>
        </li>
        {
          (list && list.length > 0) ? list.map((item, key) => {
            return (
              <li key={key}>
                <span className='col-1'>
                  {
                    /^http:\/\/\w+\.\w/.test(item.name) ? <a target='_blank' href={item.name}>{item.name}</a> : item.name
                  }
                </span>
                <span className='col-2'>{item.pv}</span>
                <span className='col-3'><span style={{ width: `${(item.pv / item.total * 100).toFixed(2) + '%'}` }}>{`${(item.pv / item.total * 100).toFixed(2) + '%'}`}</span></span>
              </li>
            )
          }) : <div style={{ textAlign: 'center', marginTop: '120px' }}>暂无数据</div>
        }
      </ul>
      // </Spin>
    )
  }
}

export default TopList
