import React, { Component } from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import Picture from '../../../../utils/commonStyles/pictureArea'
import * as util from '../../../../utils/time';

class PictureArea extends Component {
  render () {
    const {info} = this.props
    const summary = info?[
      { title: '浏览量(PV)', value: info.pv || 0, help: 'Page View(PV)，用户每打开一个网站页面就被记录1次。用户多次打开同一页面，访问量值累计。' },
      { title: '访客数(UV)', value: info.uv || 0, help: '查询时间范围内您网站的独立访客数(以Cookie为依据)，查询时间范围内同一访客多次访问您网站只计算1个访客。' },
      { title: 'IP数', value: info.ip_num || 0, help: '查询时间范围内您网站的独立访问ip数。' },
      { title: '跳出率', value: (util.percent(info.bounce_rate)) || 0, help: '只浏览了一个页面便离开了网站的访问次数占总的访问次数的百分比。' },
      { title: '平均访问时长', value: (util.sec2time(info.ave_interview_duration)) || 0, help: '访客在一次访问中，平均打开网站的时长。即每次访问中，打开第一个页面到关闭最后一个页面的平均值，打开一个页面时计算打开关闭的时间差。' }
    ]:[]
    return <Picture summary={summary} />
  }
}

export default withRouter(connect(({ page }) => ({
  page
}))(PictureArea))
