import colors from '../../../../../utils/charts/color'

function produceOption (data) {
  // console.log(3333, data)
  let dataArr = []

  data.map((item) => {
    if (item.name && item.name.length > 10) {
      dataArr.push({
        totalName: item.name,
        name: item.name.substr(0, 10) + '……',
        value: item.pv
      })
    } else {
      dataArr.push({
        totalName: item.name,
        name: item.name,
        value: item.pv
      })
    }
  })

  let option = {
    color: colors,
    tooltip: {
      trigger: 'item',
      // formatter: '{b} : {c} ({d}%)'
      formatter: function (params) {
        return params.data.totalName + ' : ' + params.data.value + '(' + (params.percent + '%') + ')'
      }
    },
    series: [
      {
        type: 'pie',
        radius: ['70%', '80%'], // 可视容器的min(宽,高)
        center: ['50%', '50%'],
        data: dataArr,
        label: {
          normal: {
            show: true,
            textStyle: {
              fontSize: 10
            }
          }
        }
      }
    ]
  }
  return option
}

export default produceOption
