import React, { Component } from 'react'
import { Table, Input, Popover, Icon, message} from 'antd'
import * as util from '../../../../utils/time';
import pagination from '../../../../utils/page'
import styles from './index.module.less'

const { Column, ColumnGroup } = Table
const { Search } = Input

export default class TableList extends Component {
  constructor (props) {
    super(props)
    this.state = {
      loading: false,
      pagination,
      data:[],
      order:'descend',
      sort :'pv',
    }
  }

  componentWillReceiveProps(){
    this.setState({
      pagination:{
        ...this.state.pagination,
        total: this.props.total
      }
    })
  }

  changePagination = (pagination, filters, sorter) => {
    const {order, sort} = this.state
    console.log(sorter)
    this.setState({
      pagination: {
        ...this.state.pagination,
        current: pagination.current
      }
    })
    this.props.onChange(pagination, filters, sorter,this.state)
  }

  render () {
    const {data} = this.props
    return <div className={styles.table}>
      <div className='list-content'>
        <Table
          size='small'
          pagination={this.state.pagination}
          bordered
          onChange={this.changePagination}
          loading={this.state.loading}
          dataSource={data.map((item, key) => {
            return {
              key,
              ...item
            }
          })}>
          <Column title='访客标识码' title={
                        <span>
                            访客标识码 &nbsp;
                            <Popover content='访客标识码是效能系统根据访客的访问设备、系统环境、cookie等参数生成的一个用于识别唯一访客的标记。' placement="bottomLeft">
                                <Icon style={{color: '#54c073', cursor: 'pointer'}} type="question-circle"/>
                            </Popover>
                        </span>
                        } key='date' dataIndex='uuid' />
          <Column title='IP地址' key='ip' dataIndex='ip' />
          <Column title='最新访问时间' key='last_visit_time' dataIndex='last_visit_time' sorter={ (a, b)=>a.uv - b.uv } render={ text=>util.format(text)} />
          <Column title='最近浏览时间' key='last_browsing_time' dataIndex='last_browsing_time' sorter={true} render={ text=>util.format(text)} />
          <Column title='浏览量(PV)' key='pv' dataIndex='pv' sorter={true} render={ text=>util.percent(text) } />
          <Column title='访问次数' key='uv' dataIndex='uv' sorter={true} render={text => util.sec2time(text)} />
          <Column title='平均访问页数' key='ave_visit_nums' dataIndex='ave_visit_nums' sorter render={text => util.sec2time(text)} />
          <Column title='平均访问时长' key='ave_visit_duration' dataIndex='ave_visit_duration' sorter render={text => util.sec2time(text)} />
        </Table>
      </div>
    </div>
  }
}
