// import { message } from 'antd'
import moment from 'moment'
import * as apis from './server'

export default {
  state: {
    loading: false,
    period: 'hour',
    mode: ['date', 'date'],
    total: 0,
    data:[],
    date:[moment().format('YYYY-MM-DD'),moment().format('YYYY-MM-DD')],
    length:60,
    page:1,
    sort:'pv',
    order:'descend'
  },
  reducers: {
    setPageLoading (state, payload) {
      state.loading = payload
      return state
    },
    setTime (state, payload) {
      return {
        ...state,
        ...payload
      }
    },
    setData(state,payload){
      state.data = payload.areas?payload.areas:[]
      state.total = payload.total
    },
    setInfo(state,payload){
      state.info = payload.summary
    },
    setState(state,payload){
      for(let i in payload){
        state[i]=payload[i]
      }
    },
  },
  effects: dispatch => ({
    async searchDataByTime (payload) {
      this.setPageLoading(true)
      this.setTime(payload)
      this.setPageLoading(false)
    },
    async queryInfo (payload) {
      const data = await apis.visitSummary(payload)
      this.setInfo(data)
    },
    async seatch (payload,state) {
      this.setPageLoading(true)
      const {page, length, order, sort,date} = payload
      if(page){
        this.setState({page})
      }
      if(length){
        this.setState({length})
      }
      if(sort){
        this.setState({sort})
      }
      if(order){
        this.setState({order})
      }
      if(date){
        this.setState({date})
      }
      this.queryInfo({date:date?date:state.area.date})
      const data = await apis.areaList({
        page:page,
        order:order?order:state.area.order,
        length:length?length:state.area.length,
        sort:sort?sort:state.area.sort,
        date:date?date:state.area.date,
      })
      this.setData(data)
      this.setPageLoading(false)
    },
  })
}
