import React, { Component } from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { Table, Input, Button, Select, Icon, TreeSelect, message } from 'antd'
import * as util from '../../../../utils/time';
import pagination from '../../../../utils/page';
import moment from 'moment'
import styles from './index.module.less'
import * as utils from '../../../../utils/util';

const { Column, ColumnGroup } = Table
const { Search } = Input
const kong = {
  idx:null,
  key :null,
  name:'默认',
  title:'默认',
  value:null,
}

class TableList extends Component {
  static getDerivedStateFromProps(props){
    if(props.orgs){
      let {orgs} = props
      return {
        orgs:utils.setList(orgs)
      }
    }
  }
  constructor (props) {
    super(props)
    this.state = {
      loading: false,
      pagination,
      data:[],
      order:'descend',
      sort :'pv',
      org_id:'',
      orgs:[]
    }
  }
  componentWillReceiveProps(){
    this.setState({
      pagination:{
        ...this.state.pagination,
        total: this.props.total
      }
    })
  }
  changePagination = (pagination, filters, sorter) => {
    const {order, sort} = this.state
    console.log(sorter)
    this.setState({
      pagination: {
        ...this.state.pagination,
        current: pagination.current,
      }
    })
    this.props.onChange(pagination, filters, sorter,this.state)
  }

  onSearch=(value)=>{
    this.setState({
      search:value
    })
    this.props.onSearch({search:value,org_id:this.state.org_id})
  }
  changeValue = (e) => {
    this.setState({
      search: e.target.value
    })
  }
  selectValue = (value) => {
    console.log(value)
    this.setState({
      org_id: value
    })
    this.props.onSearch({org_id:value,search:this.state.search})
  }
  export = () => {
    const {data}= this.props
    if(data){
      utils.down(data,document)
    }else{
      message.error('暂无数据提供下载')
    }
  }
  render () {
    const {data}= this.props
    const {orgs}= this.state
    console.log(orgs)
    return <div className={styles.table}>
      <div className='export'>
        <div className='title-search'>
          <Search placeholder="标题搜索" onInput={this.changeValue} onSearch={this.onSearch} enterButton />
        </div>
        <div className='title-search'>
          <TreeSelect
            style={{ width: 170 }}
            value={this.state.org_id}
            dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
            treeData={orgs.concat(kong)}
            placeholder="所属组织"
            treeDefaultExpandAll
            allowClear
            onChange={this.selectValue}
          />
        </div>
        <div className='export-button'>
          <Button onClick={this.export} type='primary'>导出数据</Button>
          <a href="" download="发稿量统计.xlsx" id="hf"></a>
        </div>
      </div>
      <div className='list-content'>
        <Table
          size='small'
          pagination={this.state.pagination}
          bordered
          onChange={this.changePagination}
          loading={this.state.loading}
          dataSource={data.map((item, key) => {
            return {
              key,
              ...item
            }
          })}>
          <Column title='登录名' key='login_name' dataIndex='login_name' />
          {/* <ColumnGroup title='网站基础指标'> */}
            
            <Column title='姓名' key='name' dataIndex='name' render={ text=>util.format(text)} />
            <Column title='所属组织' key='org' dataIndex='org' render={ text=>util.format(text)} />
          {/* </ColumnGroup> */}
          {/* <ColumnGroup title='流量质量指标'> */}
            <Column title='文章数' key='article_nums' dataIndex='article_nums' sorter render={ text=>util.percent(text) } />
            <Column title='文章总数' key='history_article_nums' dataIndex='history_article_nums' sorter render={text => util.sec2time(text)} />
            <Column title='浏览量(PV)' key='pv' dataIndex='pv' sorter render={ text=>util.format(text) } />
            <Column title='评论数' key='comment_nums' dataIndex='comment_nums' sorter render={ text=>util.percent(text) } />
          {/* </ColumnGroup> */}
        </Table>
      </div>
    </div>
  }
}

export default withRouter(connect(({ publish }) => ({
  publish
}), ({ publish: { searchDataByTime } }) => ({
  searchDataByTime
}))(TableList))
