
function produceOption (data) {
  let maxNum = 0
  if (data) {
    data.map((item) => {
      // item.name = unescape(item.name)
      maxNum = Math.max(maxNum, item.value)
    })
  }

  // console.log('data333', data)

  var option = {
    visualMap: {
      min: 0,
      max: maxNum,
      realtime: false,
      show: false,
      calculable: true,
      inRange: {
        color: ['#51a9f3', '#b2dafc']
      }
    },
    series: [
      {
        type: 'map',
        mapType: 'china',
        // slient: true,
        mapLocation: {
          top: '20%'
        },
        data: data
      }
    ],
    animation: false
  }
  return option
}

export default produceOption
