
import React, {Component} from 'react';
import {Radio} from 'antd';
import moment from 'moment'

const {Button: RadioButton, Group: RadioGroup} = Radio;

class PeriodSelect extends Component {

    static defaultProps = {
        date: [],
        onChange: f => f,
        defaultPeriod: "day"
    };

    constructor(props) {
        super(props);
        this.onChange = this.onChange.bind(this);
    }

    onChange(event) {
        const value = event.target.value;
        this.props.onChange({componentName: 'PeriodSelect', value});
    }

    /**
     *
     * @returns {*}
     */
    render() {
        const {defaultPeriod, date, mode} = this.props;

        // console.log('defaultPeriod', defaultPeriod, date, mode)

        let begin = moment(date[0]).format('YYYY-MM-DD')
        let end = moment(date[1]).format('YYYY-MM-DD')

        return (
            <RadioGroup onChange={this.onChange} defaultValue={defaultPeriod} size="small">
                <RadioButton disabled={begin != end && mode[0] != 'month'} value="hour">按时</RadioButton>
                <RadioButton value="day">按日</RadioButton>
                <RadioButton value="month">按月</RadioButton>
            </RadioGroup>
        );
    }
}

export default PeriodSelect;