import React, { Component } from 'react'
// import moment from 'moment'
import Picture from '../../../../utils/commonStyles/pictureArea'

// const Column = Table.Column

class PictureArea extends Component {
  render () {
    const {info} = this.props
    const summary = info?[
      { title: '频道总数', value: info.channel_count || 0, help: '当前站点所有频道总数量。' },
      { title: '文章数', value: info.articleCount || 0, help: '当前统计时间范围内某一作者的文章数量。' },
      { title: '浏览量(PV)', value: info.pvTotal || 0, help: 'Page View(PV)，用户每打开一个网站页面就被记录1次。用户多次打开同一页面，访问量值累计。' },
      { title: '访客数(UV)', value: info.uvTotal || 0, help: '查询时间范围内您网站的独立访客数(以Cookie为依据)，查询时间范围内同一访客多次访问您网站只计算1个访客。' },
      { title: 'IP数', value: info.ipTotal || 0, help: '查询时间范围内您网站的独立访问ip数。' }
    ]:[]
    return <Picture summary={summary} />
    
  }
}

export default PictureArea
