import React, { Component } from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { Table } from 'antd'
import * as util from '../../../../utils/time';
import pagination from '../../../../utils/page'
import moment from 'moment'
import styles from './index.module.less'

const { Column, ColumnGroup } = Table


class TableList extends Component {
  constructor (props) {
    super(props)
    this.state = {
      loading: false,
      pagination
    }
  }
  componentWillReceiveProps(){
    this.setState({
      pagination:{
        ...this.state.pagination,
        total: this.props.total
      }
    })
  }

  changePagination = (pagination) => {
    // console.log(pagination)
    this.setState({
      pagination: {
        ...this.state.pagination,
        current: pagination.current
      }
    })
    // console.log('pagination', pagination)
  }
  changePagination = (pagination, filters, sorter) => {
    const {order, sort} = this.state
    console.log(sorter)
    this.setState({
      pagination: {
        ...this.state.pagination,
        current: pagination.current
      }
    })
    this.props.onChange(pagination, filters, sorter,this.state)
  }

  render () {
    const {data}= this.props
    return <div className={styles.table}>
      <Table
        size='small'
        pagination={this.state.pagination}
        bordered
        onChange={this.changePagination}
        loading={this.state.loading}
        dataSource={data.map((item, key) => {
          return {
            key,
            ...item
          }
        })}>
        <Column title='日期' key='label' dataIndex='label' sorter/>
        <ColumnGroup title='网站基础指标'>
          <Column title='浏览量(PV)' key='pv' dataIndex='pv' sorter render={ text=>util.format(text) } />
          <Column title='访客数(UV)' key='uv' dataIndex='uv' sorter render={ text=>util.format(text)} />
          <Column title='IP数' key='ip_num' dataIndex='ip_num' sorter render={ text=>util.format(text)} />
        </ColumnGroup>
        <ColumnGroup title='流量质量指标'>
          <Column title='跳出率' key='bounce_rate' dataIndex='bounce_rate' sorter render={ text=>util.percent(text) } />
          <Column title='平均访问时长' key='ave_interview_duration' dataIndex='ave_interview_duration' sorter render={text => util.sec2time(text)} />
        </ColumnGroup>
      </Table>
    </div>
  }
}

export default withRouter(connect(({ web }) => ({
  web
}), ({ web: { searchDataByTime } }) => ({
  searchDataByTime
}))(TableList))
