/* eslint-disable compat/compat */
/* eslint-disable no-param-reassign */
import * as apis from './server'
import moment from 'moment'

export default {
  state: {
    pvAndUvForTenMinutes: {},
    otherData: {},
    timestamp: moment(new Date()).valueOf()
  },
  reducers: {
    setTenTrends (state, payload) {
      state.pvAndUvForTenMinutes = payload
      state.timestamp = moment(new Date()).valueOf()
    },
    setCharts (state, payload) {
      state.otherData = payload
    }
  },
  effects: dispatch => ({
    async getPVandUV () {
      let res = await apis.currentVisitorNum()
      let trend = res.trends
      this.setTenTrends(trend)
    },
    async getOtherCharts () {
      let res = await apis.overflowData()
      let obj = {
        visits: res.visits,
        channels: res.channels,
        pages: res.pages,
        map: res.map
      }
      this.setCharts(obj)
    }
  })
}
