// import { login } from '../../services/login'
// import { message } from 'antd'
import * as apis from "./server"
import moment from 'moment'

export default {
  state: {
    loading: false,
    period: 'hour',
    mode: ['date', 'date'],
    total: 0,
    data:[],
    date:[moment(new Date()).add(-1, 'days').format('YYYY-MM-DD'),moment(new Date()).add(-1, 'days').format('YYYY-MM-DD')],
    length:60,
    page:1,
    sort:'pv',
    order:'descend',
    publish:'',
    org_id:'',
    search:'',
    circle:[],
    trendData:null,
  },
  reducers: {
    setPageLoading (state, payload) {
      state.loading = payload
      return state
    },
    setData(state,payload){
      state.data = payload.creators || []
      state.total = payload.total || 0
    },
    setInfo(state,payload){
      state.info = payload.summary
    },
    setTrend(state,payload){
      state.trendData = payload.trends ||null
      state.circle = payload.circle ||[]
    },
    setOrgs(state,payload){
      state.orgs = payload.orgs ||[]
    },
    setState(state,payload){
      for(let i in payload){
        state[i]=payload[i]
      }
    },
  },
  effects: dispatch => ({
    async searchDataByTime (payload) {
      this.setPageLoading(true)
      this.setTime(payload)
      this.setPageLoading(false)
    },
    async queryTrend (payload,state) {
      const {sort} = payload
      const data = await apis.visitTrend({
        date:state.publish.date,
        sort
      })
      this.setTrend(data)
    },
    async queryInfo (payload) {
      const data = await apis.visitSummary(payload)
      this.setInfo(data)
    },
    async queryOrgs (payload) {
      const data = await apis.queryOrgs(payload)
      this.setOrgs(data)
    },
    async seatch (payload,state) {
      this.setPageLoading(true)
      const {page, length, order, sort,date, org_id, search} = payload
      if(page){
        this.setState({page})
      }
      if(length){
        this.setState({length})
      }
      if(sort){
        this.setState({sort})
      }
      if(order){
        this.setState({order})
      }
      if(date){
        this.setState({date})
        this.queryTrend({date})
      }
      this.queryInfo({date:date?date:state.publish.date})
      const data = await apis.creatorList({
        page:page,
        order:order?order:state.publish.order,
        length:length?length:state.publish.length,
        sort:sort?sort:state.publish.sort,
        date:date?date:state.publish.date,
        org_id:org_id?org_id:state.publish.org_id,
        search:search?search:state.publish.search,
      })
      this.setData(data)
      this.setPageLoading(false)
    },
  })
}
