import colors from '../../../../utils/charts/color'

function produceOption (data) {
  let option = {
    color: colors,
    xAxis: {
      name: 'value',
      show: false
    },
    yAxis: {
      type: 'category',
      axisLine: {
        show: false
      },
      axisTick: {
        show: false
      },
      axisLabel: {
        color: '#fff'
      },
      inverse: true,
      data: data ? data.map(item => item.name) : []
    },
    grid: {
      height: '80%',
      width: '50%',
      left: '30%',
      top: 'center'
    },
    series: [
      {
        type: 'bar',
        silent: false,
        barWidth: '12px',
        barGap: '100%',
        label: {
          normal: {
            position: 'right',
            show: true
          }
        },
        data: data ? data.map((item, index) => {
          return {
            value: item.pv,
            itemStyle: {
              color: colors[index % colors.length]
            }
          }
        }) : []
      }
    ]
  }
  return option
}

export default produceOption
