import React, { Component } from 'react'
// import styles from './index.less'
import styles from './index.module.less'
import CountUp from 'react-countup'

/**
 * 需要传入参数
 * title 名字
 * value 值 / 初始值
 * valueStyle 框中值样式
 * frameStyle 大框的样式
 * valueFrameStyle value框的样式
 * countUpObj 计数效果相关配置
 *   -- end 结束数字
 *   -- duration 滚动时长
 * href 跳转
 */

class NumberFrame extends Component {
  jump = () => {
    // console.log(this.props)
    const { href } = this.props
    if(href){
      window.location.href = href
      // this.props.history.push(href)
    }
  }
  render () {
    const { title, value, valueStyle, frameStyle, valueFrameStyle, countUpObj } = this.props

    return <div className={styles.labelFrame} style={frameStyle}>
      {title && <div className='title'>{title}</div>}
      {(value || value === 0) && <div className={'value-frame'} style={valueFrameStyle} onClick={this.jump}>
        <div className='value-x' />
        <div className='value-y' />
        <div className='value-dom' />
        <div className='value' style={valueStyle}>
          {countUpObj ? <CountUp
            start={value}
            end={countUpObj ? countUpObj.end : value}
            duration={countUpObj.duration || 2}
          /> : value}
        </div>
      </div>}
    </div>
  }
}

export default NumberFrame
