import React, { Component } from 'react'
// import styles from './index.less'
import LabelComponent from '../../../../utils/commonStyles/numberFrame'
import styles from './index.module.less'

class Label extends Component {
  constructor (props) {
    super(props)
    this.state = {
      todaypv: 0,
      todayuv: 0,
      yesterdaypv: 0,
      yesterdayuv: 0
    }
  }

  componentWillReceiveProps (nextProps) {
    // nextProps.data.today.pv = parseInt(Math.random() * 10000 + 1)
    setTimeout(() => {
      this.setState({
        todaypv: (nextProps.data && nextProps.data.today && nextProps.data.today.pv) || 0,
        todayuv: (nextProps.data && nextProps.data.today && nextProps.data.today.uv) || 0,
        yesterdaypv: (nextProps.data && nextProps.data.yesterday && nextProps.data.yesterday.pv) || 0,
        yesterdayuv: (nextProps.data && nextProps.data.yesterday && nextProps.data.yesterday.uv) || 0
      })
    })
  }

  render () {
    const { data } = this.props
    // console.log('label', this.props)
    const { todaypv, todayuv, yesterdaypv, yesterdayuv } = this.state

    return <div className={styles.labelFrame}>
      <LabelComponent title={'今日访客'}
        value={todaypv}
        countUpObj={{
          end: data.today ? data.today.uv : 0,
          duration: 2
        }} />
      <LabelComponent title={'今日访问量'}
        value={todayuv}
        countUpObj={{
          end: data.today ? data.today.pv : 0,
          duration: 2
        }} />
      <LabelComponent title={'昨日访客'}
        value={yesterdaypv}
        countUpObj={{
          end: data.yesterday ? data.yesterday.uv : 0,
          duration: 2
        }} />
      <LabelComponent title={'昨日访问量'}
        value={yesterdayuv}
        countUpObj={{
          end: data.yesterday ? data.yesterday.pv : 0,
          duration: 2
        }} />
    </div>
  }
}

export default Label
