
import React, { Component } from 'react'
import { Radio } from 'antd'
import moment from 'moment'
import './overwrite.css'

const { Button: RadioButton, Group: RadioGroup } = Radio

class SimpleDatePicker extends Component {
  constructor (props) {
    super(props)
    this.onChange = this.onChange.bind(this)
  }

  onChange (event) {
    const value = event.target.value.split(',').map((item) => {
      return moment(item)
    })
    this.props.onChange({ componentName: 'SimpleDatePicker', value })
  }

  /**
     *
     * @returns {*}
     */
  render () {
    const { currentPeriod, Period, slot} = this.props
    const formatDate = this.props.date.map((item) => {
      return item.format('YYYY-MM-DD')
    }).join(',')

    return (
      <RadioGroup onChange={this.onChange} value={formatDate} size='small' disabled={this.props.disabled}>
        <RadioButton disabled={slot&& slot==='today'} value={[moment().format('YYYY-MM-DD'), moment().format('YYYY-MM-DD')].join(',')}>今天</RadioButton>
        <RadioButton value={[moment().subtract(1, 'days').format('YYYY-MM-DD'), moment().subtract(1, 'days').format('YYYY-MM-DD')].join(',')}>昨天</RadioButton>
        <RadioButton disabled={(Period && currentPeriod === 'hour')} value={[moment().subtract(6, 'days').format('YYYY-MM-DD'), moment().format('YYYY-MM-DD')].join(',')}>最近7天</RadioButton>
        <RadioButton disabled={(Period && currentPeriod === 'hour')} value={[moment().subtract(29, 'days').format('YYYY-MM-DD'), moment().format('YYYY-MM-DD')].join(',')}>最近30天</RadioButton>
      </RadioGroup>
    )
  }
}

export default SimpleDatePicker
