import colors from '../../../../../utils/charts/color'

function produceOption (data, data2, label) {
  console.log(data)
  // 模拟数据
  // legendname
  let legendName = []
  // x轴
  let xLabel = []
  // series 数据
  let series = []
  for (var key in data[0]) {
    legendName.push(key)
    series.push({
      name: key,
      type: 'line',
      data: []
    })
    data[0][key].map((item) => {
      series[series.length - 1].data.push(item[label])
      if (legendName.length == 1) {
        xLabel.push(item.label)
      }
    })
  }

  let dataArr = []
  data2.map((item) => {
    dataArr.push({
      name: item.name,
      value: item[label]
    })
  })

  series.push({
    type: 'pie',
    // silent: true,
    radius: ['40%', '50%'], // 可视容器的min(宽,高)
    center: ['12%', '45%'],
    data: dataArr
  })

  const option = {
    color: colors,
    legend: {
      icon: 'circle',
      // data: data.lineLabelName,
      data: legendName,
      textStyle: {
        color: '#647897'
      },
      bottom: 0
    },
    tooltip: {
      trigger: 'axis'
    },
    grid: {
      height: '70%',
      width: '70%',
      left: '28%',
      top: '20px'
    },
    xAxis: [
      {
        type: 'category',
        axisLabel: {
          color: '#647897'
        },
        axisLine: false,
        data: xLabel
      }
    ],
    yAxis: [
      {
        type: 'value',
        axisLabel: {
          color: '#647897'
        },
        axisLine: false,
        splitLine: {
          show: true,
          lineStyle: {
            color: ['#647897'],
            width: 1,
            type: 'solid'
          }
        }
      }
    ],
    series: series
  }

  return option
}

export default produceOption
