import colors from '../../../../../utils/charts/color'

function produceOption (data) {
  // console.log('data1111', data)
  let dataArr = []

  data.map((item) => {
    dataArr.push({
      name: item.name,
      value: item.pv
    })
  })

  let option = {
    color: colors,
    tooltip: {
      trigger: 'item',
      formatter: '{a} <br/>{b} : {c} ({d}%)'
    },
    series: [
      {
        type: 'pie',
        radius: ['70%', '80%'], // 可视容器的min(宽,高)
        center: ['50%', '50%'],
        data: dataArr
      }
    ]
  }
  return option
}

export default produceOption
