import colors from '../../../../../utils/charts/color'

function produceOption (data, label) {
  // console.log('label', label)
  // 模拟数据

  let nameObj = {
    'pv': '浏览量(PV)',
    'uv': '访客量(UV)',
    'ip_num': 'IP数',
    'bounce_rate': '跳出率',
    'ave_interview_duration': '平均访问时长'
  }

  // x轴
  let xLabel = []
  let yLabel = []

  if (data) {
    data.map((item) => {
      xLabel.push(item.label)
      yLabel.push(item[label])
    })
  }

  // console.log('dataname', dataName)

  let option = {
    color: colors,
    // grid: { containLabel: true },
    tooltip: {
      trigger: 'axis'
    },
    legend: {
      data: [nameObj[label]],
      bottom: 20,
      icon: 'circle',
      textStyle: {
        color: '#647897'
      }
    },
    xAxis: {
      type: 'category',
      axisLine: {
        show: false
      },
      data: xLabel,
      axisLabel: {
        color: '#fff'
      }
    },
    yAxis: {
      type: 'value',
      axisLine: {
        show: false
      },
      axisLabel: {
        color: '#647897'
      },
      splitLine: {
        show: true,
        lineStyle: {
          color: ['#647897'],
          width: 1,
          type: 'solid'
        }
      }
    },
    grid: {
      // left: '20%',
      top: '50',
      height: '60%',
      color: '#647897'
    },
    series: [
      {
        name: nameObj[label],
        type: 'line',
        data: yLabel
      }
    ]
  }
  return option
}

export default produceOption
