import colors from '../../../../../utils/charts/color'

function produceOption (data2,lable) {
  const option = {
    color: colors,
    grid: {
      height: '100%',
      width: '40%',
      left: '50%',
      top: '0px'
    },
    visualMap: [
      {
        show: false,
        seriesIndex: 0,
        inRange: {
          color: ['#51a9f3', '#b2dafc']
        }
      },
      {
        orient: 'horizontal',
        seriesIndex: 1,
        show: false,
        inRange: {
          color: ['#51a9f3', '#b2dafc']
        }
      }
    ],
    xAxis: [
      {
        type: 'value',
        axisLabel: false,
        axisLine: false,
        splitLine: {
          show: false,
          lineStyle: {
            color: ['#647897'],
            width: 1,
            type: 'solid'
          }
        }
      }
    ],
    yAxis: [
      {
        type: 'category',
        axisLabel: {
          color: '#fff'
        },
        axisLine: false,
        inverse: true,
        data: (function () {
          var res = []
          data2.map((item) => {
            res.push(item.area)
          })
          return res
        })()
      }
    ],
    series: [
      {
        type: 'map',
        mapType: 'china',
        slient: false,
        mapLocation: {
          top: '10%',
          left: '10%'
        },
        data: (function () {
          var res = []
          data2.map((item) => {
           res.push({name:item.area,value:item[lable]})
          })
          return res
        })(),
      },
      {
        name: data2.name,
        type: 'bar',
        barWidth: '12px',
        data: (function () {
          var res = []
          data2.map((item) => {
           res.push({name:item.area,value:item[lable]})
          })
          return res
        })(),
        label: {
          normal: {
            position: 'right',
            show: true
          }
        }
      }
    ],
    animation: false
  }

  return option
}

export default produceOption
