import React, { Component } from 'react'
import { Breadcrumb } from 'antd'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import DimensionsDropdown from '../../../../utils/commonStyles/labelSelect'
import styles from './index.module.less'
import barOption from './chartOption'
import Chart from '../../../../utils/charts/chart'
import Back from '../../../../assets/img/back.png'
import moment from 'moment'
import { Icon } from 'antd'
import _ from 'lodash'

const goals = [
  { label: '浏览量(PV)', value: 'pv' },
  { label: '文章数', value: 'id' },
  { label: '访客数(UV)', value: 'uv' },
  { label: 'IP数', value: 'ipTotal' }
]
let obj ={
  idx:0,
  name:[],
}
class ChartArea extends Component {
  static getDerivedStateFromProps(props,state){
    return {
      timestamp: moment(new Date()).valueOf(),
    }
  }
  constructor(props){
    super(props)
    this.state = {
      dimensions: ['pv'],
      status: true,
      timestamp: moment(new Date()).valueOf(),
      stack:[],
      data: [],
      lable:'pv'
    }
  }

  componentDidMount(){
    this.setState({
      data:this.props.data
    },()=>{
      this.setState({
        timestamp: moment(new Date()).valueOf(),
      })
    })
  }
  onDimensionsChange = (data) => {
    this.setState({
        dimensions: [data],
        lable:data,
    },()=>{
      this.setState({
        timestamp: moment(new Date()).valueOf()
      })
    });
  }

  changeLabel = () => {
    this.setState({
      status: !this.state.status
    })
  }

  backTrend =()=>{
    this.props.chagetrend(obj[obj.idx])
    obj.idx--
    obj.name.pop()
    this.setState({
      timestamp: moment(new Date()).valueOf()
    })
  }
  gohomeTrend =()=>{
    this.props.chagetrend(obj[1])
    obj.idx=0
    obj.name =[]
    this.setState({
      timestamp: moment(new Date()).valueOf()
    })
  }
  goTrend=(idx)=>{
    console.log(idx)
    for (let i = obj.name.length - 1; i >= 0; i--) {
      if (i>=idx) {
          obj.name.splice(i+1, 1);
      }
    }
    obj.name.slice()
    this.props.chagetrend(obj[idx+2])
    console.log(obj[idx+2])
    obj.idx=idx+1
    this.setState({
      timestamp: moment(new Date()).valueOf()
    })
  }
  changeCategory = (event) => {
    console.log(event)
    const {data}=this.props
    const dataIndex = event.dataIndex;
    if(data[dataIndex].children){
      const arr =[]
      obj.idx++
      obj[obj.idx]=data
      obj.name.push(event.name)
      this.props.chagetrend(data[dataIndex].children)
      this.setState({
        timestamp: moment(new Date()).valueOf()
      })
    }
  }

  setStack(index) {
    this.setState({
        stack: this.state.stack.slice(0, index),
        timestamp: (new Date()).getTime()
    });
  }

  getBreadcrumb(data, stack) {
    const breadcrumb = [];
    stack.map(index => {
        breadcrumb.push(data[index].name);
        if (data[index].hasOwnProperty('children') && data[index].children.length > 0) {
            data = data[index].children;
        }
    });
    return breadcrumb;
  }

  render () {
    const {lable} = this.state
    const {data} = this.props
    obj[obj.idx+1]=data
    return <div style={{clear:'both',marginTop: '120px'}}>
      {data &&<div style={this.state.status ? {display: 'block'} : {display: 'none'}} className={styles.chartArea}>
        <DimensionsDropdown
          options={goals}
          checkCount={1}
          defaultValue={['pv']}
          onChange={this.onDimensionsChange}
          getPopupContainer={node => {
            return node.parentNode.parentNode
          }} />
          <div style={{color: '#758FB2', marginTop: '10px'}}>
            <Breadcrumb>
                <Breadcrumb.Item onClick={()=>{this.setStack(0)}}>
                  <span style={{cursor:'default'}} onClick={this.gohomeTrend}>所有频道</span>
                 { obj.name.map((i,idx)=>{
                    return <span style={{cursor:'default'}} onClick={(e)=>{this.goTrend(idx)}}>/{i}</span>
                  })}
                </Breadcrumb.Item>
                {
                    this.getBreadcrumb(this.state.data, this.state.stack).map((item, index)=>{
                        return (
                            <Breadcrumb.Item key={index} onClick={()=>{this.setStack(index + 1);}}>{item}</Breadcrumb.Item>
                        )
                    })
                }
            </Breadcrumb>
          </div>
        <div className='chart-main'>
          <Chart domName={'selectChartOption'} onClick={this.changeCategory} type={'bar'} option={barOption(data,lable)} timestamp={this.state.timestamp}/>
         {obj.idx>0 &&<div className='back' onClick={this.backTrend}><img src={Back}/></div>}
        </div>
      </div>}
      <div className={styles.chartArea} style={!this.state.status ? {display: 'block'} : {display: 'none'}}>
        <div style={{color: '#B7D5FE'}}>图形报表</div>
      </div>

      <div className={styles.close} onClick={this.changeLabel}>
        {this.state.status ? <Icon type="double-left" style={{transform:'rotate(90deg)'}}/>
        : <Icon type="double-left" style={{transform:'rotate(270deg)'}}/>}
      </div>
    </div>
  }
}

export default withRouter(connect(({ category }) => ({
  category
}), ({ category: { searchDataByTime } }) => ({
  searchDataByTime
}))(ChartArea))
