import React, { Component } from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import Search from '../../../utils/commonStyles/timeSearch/timeSearch'
import PictureArea from './pictureArea'
import ChartArea from './chartArea'
import {relTime} from '../../../utils/util'
import TableList from './tableList'
import pagination from '../../../utils/page'
import styles from './index.module.less'
import moment from 'moment'

class Publish extends Component {
  constructor (props) {
    super(props)
    this.state = {
      data:[],
      order:'descend',
      sort :'pv',
    }
  }
  componentDidMount(){
    const {order, sort} = this.state
    this.props.seatch({
      page:pagination.current,
      length:pagination.pageSize,
      order,
      sort,
      date:[moment().format('YYYY-MM-DD'),moment().format('YYYY-MM-DD')],
    })
    this.props.queryOrgs()
    this.props.queryTrend({
      sort:'pv'
    })
  }
  setch=(value)=>{
    this.props.seatch({date:relTime(value)})
  }
  changeLable=(value)=>{
    this.props.queryTrend({sort:value})
  }
  onSearch=(payload)=>{
    this.props.seatch(payload)
  }
  onChange=(pagination, filters, sorter)=>{
    this.props.seatch({
      page:pagination.current,
      length:pagination.pageSize,
      order:sorter&& sorter.order,
      sort:sorter && sorter.field,
    })
  }
  render () {
    console.log(this.props.publish)
    return (
      <div className={styles.webComponent}>
        <Search title={'发稿量统计'} simpleDate RangeDate onChange={this.setch}/>
        <div className='web-main'>
          <PictureArea info = {this.props.publish.info}/>
          <ChartArea info={this.props.publish.circle} data={this.props.publish.trendData?[this.props.publish.trendData]:[]} changeLable={this.changeLable}/>
          <TableList total={this.props.publish.total} orgs={this.props.publish.orgs?[this.props.publish.orgs]:[]}  onChange={this.onChange} data={this.props.publish.data} onSearch={this.onSearch}/>
        </div>
      </div>

    )
  }
}

export default withRouter(connect(({ publish }) => ({
  publish
}), ({ publish: { seatch, queryTrend, queryOrgs} }) => ({
  seatch,
  queryTrend,
  queryOrgs
}))(Publish))
