import React, { Component } from 'react'
import { Icon, Radio, Dropdown, Menu } from 'antd'
// import { Sider } from '../commonComponents'
// import styles from './index.module.less'
import moment from 'moment'
import styles from './index.module.less'
import mapOption from './chartOption/map_option'
import Chart from '../../../../utils/charts/chart'

class TopTenCategory extends Component {
  constructor (props) {
    super(props)
    this.state = {
      minNum: 10,
      currentStatus:'list',
      colNames:[
        '频道',
        '浏览量(PV)',
        '占比'
      ],
      timeRefresh: moment(new Date()).valueOf()
    }
  }

  componentDidMount(){

  }

  componentWillReceiveProps(nextProps){
    // console.log(3333, nextProps)
    this.setState({
      timeRefresh: moment(new Date()).valueOf()
    })
  }

  jump = () => {
    this.props.history.push('/main/page/area')
  }
  
  render () {
    const { overflow: { otherData: {map} } } = this.props
    return (
      <div className={styles.webVisitor}>
        <div className='web-visitor'>
          <div className='header'>
            地域分布
            <div className='frame-icon' onClick={this.jump}>
              <Icon type="right-circle" />
            </div>
          </div>
          <div className='main'>
            <Chart domName={'area'} type={'map'} option={mapOption(map)} timestamp={this.state.timeRefresh}/>
          </div>
          <div className='img-label'>
            <img src={require('../../../../assets/img/label.png')} />
          </div>
        </div>
      </div>
    )
  }
}

export default TopTenCategory
