
function produceOption (data) {
  var option = {
    tooltip: {
      trigger: 'item',
      formatter: function (params) {
        // console.log('params', params)
        if (params && params.name && params.data) {
          return '<div>' +
          '<div>' + params.name + '</div>' +
          '<div>今日访问量 ' + params.data.pv + '</div>' +
          '<div>今日访客量 ' + params.data.uv + '</div>' +
       '</div>'
        } else {
          return '<div>' +
          '<div>' + params.name + '</div>' +
          '<div>今日访问量0</div>' +
          '<div>今日访客量0</div>' +
       '</div>'
        }
      }
    },
    series: [
      {
        name: '发稿量',
        type: 'map',
        mapType: 'china',
        mapLocation: {
          x: 'bottom'
        },
        itemStyle: {
          normal: {
            areaColor: '#1C245C', // 区域颜色
            borderColor: '#2F3F9A'
          },
          emphasis: {
            label: { show: false },
            areaColor: '#3F51C7'
          }
        },
        data: data
      }
    ],
    animation: false
  }
  return option
}

export default produceOption
