import request from '../../../service'

export async function creatorList (params) {
  return request('/creator', {
    method: 'get',
    data: params
  })
}
export async function visitSummary (params) {
  return request('/creator/summary', {
    method: 'get',
    data: params
  })
}
export async function visitTrend (params) {
  return request('/creator/trends', {
    method: 'get',
    data: params
  })
}
export async function queryOrgs (params) {
  return request('/creator/orgs', {
    method: 'get',
    data: params
  })
}