import React, { Component } from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import {
  Form,
  Input,
  Icon,
  Checkbox,
  Button
} from 'antd'
import './index.css'
import styles from  './index.module.less'
// import services from '../../services'
const FormItem = Form.Item

class Login extends Component {

  handleSubmit = (e) => {
    e.preventDefault()
    // this.props.history.push('main/dashboard')
    let { loginInfo } = this.props

    this.props.form.validateFields((err, values) => {
      if (!err) {
        loginInfo({
          ...values
        })
      }
    })

  }

  render () {
    const { form: {getFieldDecorator} } = this.props
    return <div className='login-frame'>
      <div className={styles.title}>
        山西日报中央厨房效能系统
      </div>
      <div className='login-bgImg'>
        
        <div className='login-form'>
          <h2 style={{marginBottom:'30px'}}>管理账户登录</h2>
          <Form onSubmit={this.handleSubmit}>
            <FormItem style={{marginBottom: '40px'}}>
              {getFieldDecorator('username', {
                rules: [{ required: true, message: '请填写账户名' }]
              })(
                <Input size="large" prefix={<Icon type='user' style={{ color: 'rgba(0,0,0,.25)' }}/>}
                  allowClear
                  placeholder={'请输入用户名'} />
              )}
            </FormItem>
            <FormItem style={{marginBottom: '40px'}}>
              {getFieldDecorator('password', {
                rules: [{ required: true, message: '请填写密码' }]
              })(
                <Input type='password' size="large" prefix={<Icon type='lock' style={{ color: 'rgba(0,0,0,.25)' }} />}
                  allowClear
                  placeholder={'请输入管理密码'} />
              )}
            </FormItem>
            <FormItem style={{marginBottom: '68px'}}>
              {/* {getFieldDecorator('isRember', {
                valuePropName: 'checked'
              })(<Checkbox>Remember 7 days</Checkbox>)}
              <a className='login-form-forgot' href='#/findPw'>
              Forget the password
              </a> */}
              <button htmltype='submit' className='login-form-button'>
              登 录
              </button>
              {/* <a href='#/register'>Sign Up</a> */}
            </FormItem>
          </Form>
          <div className='login-logo'></div>
        </div>
      </div>
    </div>
  }
}

const LoginForm = Form.create({ name: 'normal_login' })(Login)

export default withRouter(connect(({ login }) => ({
  login
}), ({ login: { loginInfo } }) => ({
  loginInfo
}))(LoginForm))
