import React, { Component } from 'react'
import { Dropdown, Icon, Checkbox, Radio, Button } from 'antd'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import _ from 'lodash'
import styles from './index.module.less'
// import moment from 'moment'
// import Picture from '../../../../utils/commonStyles/pictureArea'

// const Column = Table.Column

class labelChart extends Component {
  constructor(props){
    super(props)
    this.state = {
      values: this.props.defaultValue,
      visible: false,
      buttonLabel: this.getOptionLabel(this.props.defaultValue)
    }
  }

  onVisible = (flag) => {
    this.setState({
        visible: flag
    });
  }  

  onClick = () => {
    this.setState({
        visible: true
    });
  }

  getValues = (values) => {
    if (_.isArray(values)) {
        // checkbox
    } else if (_.isString(values)) {
        // radio default value
        values = [values];
    } else if (_.isObject(values)) {
        // radio select event
        values = [values.target.value];
    }
    return values;
  }

  getOptionLabel(values) {
    const label = this.props.options.filter((item) =>{
        if (values.indexOf(item.value) >= 0) {
            return true;
        } else {
            return false
        }
    }).map((item) => {
        return item.label;
    }).join(',');

    return label ? `指标: ${label}` : '指标';
  }

  onChange = (values) => {
    values = this.getValues(values);
    if (values.length == 0) {
        return;
    }
    values = values.slice(-this.props.checkCount);
    this.props.onChange(values);
    this.setState({
        values: values,
        buttonLabel: this.getOptionLabel(values)
    });
  }
  onRadioChange = ({target:{value}}) => {
    this.props.onChange(value);
    this.setState({
        buttonLabel: this.getOptionLabel([value])
    });
 }
  renderContent = () => {

    // console.log('default', this.props)
    if (this.props.checkCount > 1) {
      return (
        <div className={styles.dropdown__pannel} focusable='true' selectable='false'>
          <div>提示：可同时选择{this.props.checkCount}项</div>
          <Checkbox.Group options={this.props.options} defaultValue={this.props.defaultValue} value={this.state.values} onChange={this.onChange} />
        </div>
      )
    } else {
      return (
        <div className={styles.dropdown__pannel} focusable='true' selectable='false'>
          <div>提示：可同时选择1项</div>
          <Radio.Group options={this.props.options} defaultValue={this.props.defaultValue[0]} onChange={this.onRadioChange} />
        </div>
      )
    }
  }
  render () {
    return <div>
      <Dropdown
        overlay={this.renderContent()}
        trigger={['click']}
        visible={this.state.visible}
        onVisibleChange={this.onVisible}>
        <Button onClick={this.onClick} style={{fontSize: '12px'}} size="small">{this.state.buttonLabel} <Icon type="down" /></Button>
      </Dropdown>
    </div>
  }
}

export default withRouter(connect(({ web }) => ({
  web
}), ({ web: { searchDataByTime } }) => ({
  searchDataByTime
}))(labelChart))
