class TopTenCategory extends Component {
  constructor (props) {
    super(props)
    this.state = {
      minNum: 10,
      currentStatus:'list',
      colNames:[
        '频道',
        '浏览量(PV)',
        '占比'
      ],
      timeRefresh: moment(new Date()).valueOf()
    }
  }

  componentDidMount(){

  }

  componentWillReceiveProps(nextProps){
    this.setState({
      timeRefresh: moment(new Date()).valueOf()
    })
    // console.log('nextProps', nextProps)
  }

  onChange = ({target}) => {
    this.setState({
      currentStatus: target.value
    })
  }

  jump = () => {
    this.props.history.push('/main/page/category')
  }
  
  render () {
    const { overflow: { otherData: {channels} } } = this.props
    // console.log('channels', channels)
    return (
      <div className={styles.webVisitor}>
        <div className='web-visitor'>
          <div className='header'>
            Top10受访频道
            <div className='frame-icon' onClick={this.jump}>
              <Icon type="right-circle" />
            </div>
          </div>
          <div className='radioLabel'>
            <Radio.Group size='small' onChange={this.onChange} value={this.state.currentStatus} buttonStyle="solid">
              <Radio.Button value="list">
                <Icon type='menu' />
              </Radio.Button>
              <Radio.Button value="chart">
                <Icon type="pie-chart" />
              </Radio.Button>
            </Radio.Group>
          </div>
          <div className='main'>
            {this.state.currentStatus === 'list' ? <TopList colNames={this.state.colNames} list={channels}/> 
            :<Chart domName={'topTenCategory'} type={'pie'} option={pieOption(channels)} timestamp={this.state.timeRefresh}/>}
          </div>
        </div>
      </div>
    )
  }
}

export default TopTenCategory
