// import moment from 'moment'
import * as apis from './server'
import moment from 'moment'

export default {
  state: {
    pvUvData: {},
    chartsData: {},
    timestamp: moment(new Date()).valueOf()
  },
  reducers: {
    setPvUvData (state, payload) {
      state.pvUvData = payload
    },
    setTime (state) {
      state.timestamp = moment(new Date()).valueOf()
    },
    setCharts (state, payload) {
      state.chartsData = payload
    }
  },
  effects: dispatch => ({
    async searchPvAndUv () {
      let data = await apis.pvAndUv()
      let pvAndUv = {
        today: data.today,
        yesterday: data.yesterday
      }
      this.setPvUvData(pvAndUv)
    },
    async searchCharts () {
      let data = await apis.charts()
      let chartsData = {
        map: data.map,
        channels: data.channels,
        areas: data.areas,
        trends: data.trends
      }
      this.setCharts(chartsData)
      setTimeout(() => {
        this.setTime()
      })
    }
  })
}
