import React, { Component } from 'react'
// import styles from './index.less'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import styles from './index.module.less'
import Label from './label'
import LabelComponent from '../../../utils/commonStyles/numberFrame'
import Chart from '../../../utils/charts/chart'
import {
  barOption,
  lineOption,
  pieOption,
  mapOption
} from './chartOption'

class Dashboard extends Component {
  componentWillMount () {
    const { searchPvAndUv, searchCharts } = this.props
    searchPvAndUv()

    searchCharts()
    // this.timer = setInterval(() => {
    //   searchPvAndUv()
    // }, 1000 * 60 * 10)

    this.timer = setInterval(() => {
      searchPvAndUv()
    }, 1000 * 60 * 10)
  }

  componentWillUnmount () {
    window.clearInterval(this.timer)
    // this.timer = null
    this.setState = (state, callback) => {
      return
    }
  }

  render () {
    const { dashboard: { pvUvData, chartsData, timestamp } } = this.props
    return <div className={styles.dashboard}>
      <div className='left-data'>
        <Label data={pvUvData} />
      </div>
      <div className='center-data'>
        <div className='detailPage'>
          <div className='title'>山西日报网站</div>
          <LabelComponent value='查看详情'
            {...this.props}
            href={'#/main/page/overview'}
            frameStyle={{
              cursor: 'pointer',
              display: 'inline-block'
            }}
            valueFrameStyle={{
              width: '120px'
            }}
            valueStyle={{
              fontSize: '18px',
              textAlign: 'center'
            }}
          />

        </div>
        <div style={{ display: 'inline-block', width: '100%', height: '100%', position: 'relative', top: '-120px' }}>
          <Chart domName={'chinaMap'} type={'map'} option={mapOption(chartsData.map)} timestamp={timestamp} />
        </div>
      </div>

      <div className='right-data'>
        <div className='chart-frame'>
          <div className='chart-title-frame' style={{ marginTop: '45px' }}>
            <div className='title'>频道访问（昨日）</div>
            <div className='whiteLine' />
          </div>
          <div className='map'>
            <Chart domName={'cateGory'} type={'bar'} option={barOption(chartsData.channels)} timestamp={timestamp} />
          </div>
        </div>
        <div className='chart-frame'>
          <div className='chart-title-frame'>
            <div className='title'>地域（昨日）</div>
            <div className='whiteLine' />
          </div>
          <div className='map'>
            <Chart domName={'area'} type={'pie'} option={pieOption(chartsData.areas)} timestamp={timestamp} />
          </div>

        </div>
        <div className='chart-frame'>
          <div className='chart-title-frame'>
            <div className='title'>前五日 PV 趋势</div>
            <div className='whiteLine' />
          </div>
          <div className='map'>
            <Chart domName={'pv'} type={'line'} option={lineOption(chartsData.trends)} timestamp={timestamp} />
          </div>
        </div>
      </div>
    </div>
  }
}

export default withRouter(connect(({ dashboard }) => ({
  dashboard
}), ({ dashboard: { searchPvAndUv, searchCharts } }) => ({
  searchPvAndUv,
  searchCharts
}))(Dashboard))
