import React, { Component } from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import Search from '../../../utils/commonStyles/timeSearch/timeSearch'
import PictureArea from './pictureArea'
import {relTime} from '../../../utils/util'
import pagination from '../../../utils/page'
import ChartArea from './chartArea'
import TableList from './tableList'
import styles from './index.module.less'
import moment from 'moment'
const timeMate = 'YYYY-MM-DD'

class Page extends Component {

  constructor (props) {
    super(props)
    this.state = {
      data:[],
      order:'descend',
      sort :'pv',
    }
  }

  componentDidMount(){
    const {order, sort} = this.state
    this.props.seatch({
      page:pagination.current,
      length:pagination.pageSize,
      order,
      sort,
      date:[moment().format('YYYY-MM-DD'),moment().format('YYYY-MM-DD')],
    })
  }
  onChange=(pagination, filters, sorter)=>{
    this.props.seatch({
      page:pagination.current,
      length:pagination.pageSize,
      order:sorter&& sorter.order,
      sort:sorter && sorter.field,
    })
  }
  onSearch=(payload)=>{
    this.props.seatch(payload)
  }
  setch=(value)=>{
    console.log(value)
    this.props.seatch({date:relTime(value)})
  }
  render () {
    const { pictureData, } = this.props
    return (
      <div className={styles.webComponent}>
        <Search title={'页面访问'} simpleDate RangeDate  onChange={this.setch}/>
        <div className='web-main'>
          <PictureArea info ={this.props.page.info}/>
          <ChartArea data={this.props.page.trend} info={this.props.page.circle}/>
          <TableList onChange={this.onChange} total={this.props.page.total} onSearch={this.onSearch} data={this.props.page.data}/>
        </div>
      </div>

    )
  }
}

export default withRouter(connect(({ page }) => ({
  page
}), ({ page: {seatch, queryInfo} }) => ({
  seatch,
  queryInfo
}))(Page))
