import React, { Component } from 'react'
// import styles from './index.less'
import {
  Menu,
  Dropdown,
  Icon
} from 'antd'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import styles from './index.module.less'

class MainLayout extends Component {
  render () {
    const { location, exit, login: { loginUserInfo } } = this.props

    const menu = (
      <Menu>
        <Menu.Item>
          <a style={{ color: 'black' }} onClick={() => { exit() }} >
          退出登录
          </a>
        </Menu.Item>
        {/* <Menu.Item>
          <a style={{ color: 'black' }} target='_blank' rel='noopener noreferrer' href='http://www.taobao.com/'>
          修改密码
          </a>
        </Menu.Item> */}
      </Menu>
    )

    let username = loginUserInfo.username || window.localStorage.getItem('username')

    return <div className={styles.headerFrame}>
      <div className='title'>
        山西日报中央厨房效能系统
      </div>
      <div className='right-part'>
        <div className='main'>
          <a className={(location.pathname === '/' || location.pathname === '/main/dashboard') ? 'fontWeight' : ''} href='#/main/dashboard'>首页</a>
        </div>
        <div className='dataDetail'>
          <a className={(location.pathname.indexOf('/main/page')) !== -1 ? 'fontWeight' : ''} href='#/main/page/overview'>数据详情页</a>
        </div>
        <div className='user'>
          <Dropdown
            getPopupContainer={node => {
              return node.parentNode.parentNode
            }} overlay={menu}>
            <a className='ant-dropdown-link' href='#'>
              <Icon type='user' /> {username}
            </a>
          </Dropdown>
        </div>
      </div>
    </div>
  }
}

export default withRouter(connect(({ login }) => ({
  login
}), ({ login: { exit } }) => ({
  exit
}))(MainLayout))
