import React, { Component } from 'react'
// import moment from 'moment'
import Picture from '../../../../utils/commonStyles/pictureArea'

// const Column = Table.Column

class PictureArea extends Component {
  render () {
    const {info}=this.props
    const summary = info?[
      { title: '录入人总数', value: info.total ||0, help: '内容管理系统CMS发布的内容的用户。' },
      { title: '文章总数', value: info.article_nums ||0, help: '当前统计时间范围内所有作者文章数合计。' },
      { title: '历史文章总数', value: info.history_article_nums ||0, help: '系统建设以来累计文章数量合计。' },
      { title: '浏览量(PV)', value: info.pv ||0, help: 'Page View(PV)，用户每打开一个网站页面就被记录1次。用户多次打开同一页面，浏览量值累计。' },
      { title: '评论总数', value: info.comment_nums ||0, help: '监控站点内文章的评论量。' }
    ]:[]
    return <Picture summary={summary} />
  }
}

export default PictureArea
