import React, { Component } from 'react'
import { Icon, Radio, Dropdown, Menu } from 'antd'
// import { Sider } from '../commonComponents'
// import styles from './index.module.less'
import styles from './index.module.less'
import lineOption from './chartOption/line_option'
import moment from 'moment'
import Chart from '../../../../utils/charts/chart'

class WebVisitor extends Component {
  constructor (props) {
    super(props)
    // console.log('lallaa',this.props)
    this.state = {
      minNum: 10,
      checkMenu: {
        'ip_num': 'IP数',
        'bounce_rate': '跳出率',
        'ave_interview_duration': '平均访问时长'
      },
      currentSelect: ['pv'],
      timeRefresh: moment(new Date()).valueOf()
    }
  }

  componentDidMount(){

  }

  componentWillReceiveProps(nextProps){
    this.setState({
      timeRefresh: moment(new Date()).valueOf()
    })
    // console.log('nextProps', nextProps)
  }

  onChange = ({target}) => {
    this.setState({
      currentSelect: [target.value],
      timeRefresh:  moment(new Date()).valueOf()
    });

  }

  onMenuSelect = ({key}) => {
    this.setState({
      currentSelect: [key],
      timeRefresh:  moment(new Date()).valueOf()
    });
  }

  getDropDownLabel = (key) => {

    if (this.state.checkMenu.hasOwnProperty(key)) {
        return this.state.checkMenu[key];
    } else {
        return '其他';
    }
  }

  jump = () => {
    this.props.history.push('/main/page/web')
  }
  

  render () {
    const { overflow: { otherData: {visits} } } = this.props
    // console.log(1111, timestampForOther)
    const menu = (<Menu onClick={this.onMenuSelect}>
      {
        Object.keys(this.state.checkMenu).map((key)=>{
            return (
                <Menu.Item key={key}>{this.state.checkMenu[key]}</Menu.Item>
            );
        })
      }
    </Menu>)
    return (
      <div className={styles.webVisitor}>
        <div className='header'>
            网站访问
            <div className='frame-icon'>
              <Icon type="right-circle" onClick={this.jump}/>
            </div>
        </div>
        <div className='radioLabel'>
          <Radio.Group size='small' onChange={this.onChange} value={this.state.currentSelect[0]} buttonStyle="solid">
            <Radio.Button value="pv">浏览量</Radio.Button>
            <Radio.Button value="uv">访客数</Radio.Button>
            <Radio.Button value={this.state.checkMenu.hasOwnProperty(this.state.currentSelect[0]) ? this.state.currentSelect[0]: 'other'} className='radio__dropdown'>
              <Dropdown.Button size="small" overlay={menu} trigger={['click']} className={['pv', 'uv'].indexOf(this.state.currentSelect[0]) == -1 ? 'dropdown_active' : null}>
                {this.getDropDownLabel(this.state.currentSelect[0])}
              </Dropdown.Button>
            </Radio.Button>
          </Radio.Group>
        </div>
        <div className='main'>
          <Chart domName={'webVisitor'} type={'line'} option={lineOption(visits, this.state.currentSelect[0])} timestamp={this.state.timeRefresh}/>
        </div>
      </div>
    )
  }
}

export default WebVisitor
