import React, { Component } from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { Table, Input } from 'antd'
import * as util from '../../../../utils/time';
import pagination from '../../../../utils/page'
import moment from 'moment'
import styles from './index.module.less'

const { Column, ColumnGroup } = Table
const { Search } = Input


class TableList extends Component {
  constructor (props) {
    super(props)
    this.state = {
      loading: false,
      pagination,
      search:''
    }
  }

  componentWillReceiveProps(){
    this.setState({
      pagination:{
        ...this.state.pagination,
        total: this.props.total
      }
    })
  }
  changePagination = (pagination, filters, sorter) => {
    const {order, sort} = this.state
    this.setState({
      pagination: {
        ...this.state.pagination,
        current: pagination.current
      }
    })
    this.props.onChange(pagination, filters, sorter,this.state)
  }
  onSearch=(value)=>{
    this.setState({
      search:value
    })
    this.props.onSearch(value)
  }
  changeSearh = e =>{
    this.setState({
      search:e.target.value
    })
  }
  render () {
    const {data} = this.props
    return <div className={styles.table}>
      <div className='title-search'>
        <Search placeholder="文章标题" onInput={this.changeSearh} onSearch={this.onSearch} enterButton />
      </div>
      <div className='list-content'>
        <Table
          size='small'
          pagination={this.state.pagination}
          bordered
          onChange={this.changePagination}
          loading={this.state.loading}
          dataSource={data.map((item, key) => {
            return {
              key,
              ...item
            }
          })}>
          <Column title='标题' key='article' dataIndex='article'/>
          <Column title='姓名' key='author_name' dataIndex='author_name' />
          <Column title='类型' key='type' dataIndex='type' />
          <Column title='频道' key='channel' dataIndex='channel' />
            <Column title='浏览量(PV)' key='pv' dataIndex='pv' sorter render={ text=>util.format(text) } />
            <Column title='访客数(UV)' key='uv' dataIndex='uv' sorter render={ text=>util.format(text)} />
            
            <Column title='平均访问时长' key='ave_interview_duration' dataIndex='ave_interview_duration' sorter render={text => util.sec2time(text)} />
            <Column title='发布时间' key='pub_time' dataIndex='pub_time' sorter />
        </Table>
      </div>
    </div>
  }
}

export default withRouter(connect(({ article }) => ({
  article
}), ({ article: { searchDataByTime } }) => ({
  searchDataByTime
}))(TableList))
