import _ from 'lodash'
import moment from 'moment'

/**
 * 安全的除法
 * @param a
 * @param b
 * @returns {number}
 */
function divide (a, b) {
  if (b == 0) {
    return 0
  } else {
    return a / b
  }
}

function percent (num) {
  if (num == 0) {
    return '0%'
  }
  return (num * 100).toFixed(2) + '%'
}

function sec2time (seconds) {
  const duration = moment.duration(Math.floor(seconds), 'seconds')
  return moment().set({
    hour: duration.hours(),
    minute: duration.minutes(),
    second: duration.seconds()
  }).format('HH:mm:ss')
}

function number_format (num, digits = 1) {
  // if (num < 1E4) {
  //     return num.toLocaleString();
  // }
  //
  // var si = [
  //     { value: 1, symbol: "" },
  //     { value: 1E4, symbol: "万" },
  //     { value: 1E6, symbol: "百万" },
  //     { value: 1E9, symbol: "亿" }
  // ];
  // var rx = /\.0+$|(\.[0-9]*[1-9])0+$/;
  // var i;
  // for (i = si.length - 1; i > 0; i--) {
  //     if (num >= si[i].value) {
  //         break;
  //     }
  // }
  if (num >= 1E6) {
    return (num / 1E4).toFixed(digits).toLocaleString() + '万'
  }
  return num.toLocaleString()
  // return (num / si[i].value).toFixed(digits).replace(rx, "$1") + si[i].symbol;
}

function format (num, digits = 1) {
  if (_.isNumber(num)) {
    return number_format(num, digits)
  } else {
    return num
  }
}

// 将moment转换为用于url传输的字符串
function dates2url (moments) {
  return moments.map(moment => moment.format('YYYY-MM-DD')).join(',')
}

/**
 * 将value从原始范围转换为目标范围的值
 * @param originRange 原始范围 [min max]
 * @param targetRange 目标范围 [min max]
 * @param value
 */
function mapRange (originRange, targetRange, origin) {
  const deltaOrigin = originRange[1] - originRange[0]
  const deltaTarget = targetRange[1] - targetRange[0]
  let k = 0
  if (deltaOrigin > 0) {
    k = deltaTarget / deltaOrigin
  }
  return parseInt((origin - originRange[0]) * k) + targetRange[0]
}

function short (text, length = 30, isStart = true) {
  let start = 0
  let end = length
  if (!isStart) {
    start = length
    end = text.length
    return text.length > length ? '...' + text.substring(start, end) : text
  } else {
    return text.length > length ? text.substring(start, end) + '...' : text
  }
}

function getUrlPath (url) {
  return url.replace(/^https?:\/\/.*?\//, '')
}

const colors = ['#3082FB', '#20F686', '#F7164B', '#FBD34A', '#94F5FF', '#FF9616', '#9E38FF', '#9AE22C', '#FF83D8', '#009D81']

export {
  divide,
  percent,
  sec2time,
  format,
  dates2url,
  short,
  getUrlPath,
  mapRange,
  colors
}
