import React, { Component } from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { Table, Input } from 'antd'
import pagination from '../../../../utils/page'
import * as util from '../../../../utils/time';
import moment from 'moment'
import styles from './index.module.less'

const { Column, ColumnGroup } = Table
const { Search } = Input

const timeMate = 'YYYY-MM-DD'

class TableList extends Component {
  constructor(props){
    super(props)
    this.state={
      loading:false,
      search:''
    }
  }
  componentWillReceiveProps(){
    this.setState({
      pagination:{
        ...this.state.pagination,
        total: this.props.total
      }
    })
  }

  changePagination = (pagination, filters, sorter) => {
    const {order, sort} = this.state
    console.log(sorter)
    this.setState({
      pagination: {
        ...this.state.pagination,
        current: pagination.current
      }
    })
    this.props.onChange(pagination, filters, sorter,this.state)
  }
  changeSeach=(e)=>{
    this.setState({
      search: e.target.value
    })
  }
  onSearch=(value)=>{
    this.setState({
      search:value
    })
    this.props.onSearch({search:value,})
  }

  render () {
    const { data} = this.props
    return <div className={styles.table}>
      <div className='title-search'>
        <Search placeholder="标题搜索" onSearch={this.onSearch} onInput ={this.changeSeach} enterButton />
      </div>
      <div className='list-content'>
        <Table
          size='small'
          pagination={pagination}
          bordered
          onChange={this.changePagination}
          loading={this.state.loading}
          dataSource={data.map((item, key) => {
            return {
              key,
              ...item
            }
          })}>
          <Column title='页面标题' key='page' dataIndex='page'/>
          <ColumnGroup title='网站基础指标'>
            <Column title='浏览量(PV)' key='pv' dataIndex='pv' defaultSortOrder="descend" sorter={true} render={ text=>util.format(text) } />
            <Column title='访客数(UV)' key='uv' dataIndex='uv' sorter={true} render={ text=>util.format(text)} />
            <Column title='IP数' key='ip_num' dataIndex='ip_num' sorter={true} render={ text=>util.format(text)} />
          </ColumnGroup>
          <ColumnGroup title='流量质量指标'>
            <Column title='跳出率' key='bounce_rate' dataIndex='bounce_rate' sorter={true} render={ text=>util.percent(text) } />
            <Column title='平均访问时长' key='ave_interview_duration' dataIndex='ave_interview_duration' sorter={true} render={text => util.sec2time(text)} />
          </ColumnGroup>
        </Table>
      </div>
    </div>
  }
}

export default withRouter(connect(({ page, common }) => ({
  page, common
}), ({ page: { searchList } }) => ({
  searchList
}))(TableList))
