import React, { Component } from 'react'
import { Form, Icon } from 'antd'
import SimpleDatePicker from './simpleDatePicker'
import RangeDatePicker from './rangeDatePicker'
import PeriodSelect from './periodSelect'
import styles from './index.module.less'
import moment from 'moment'

const FormItem = Form.Item

export default  class Search extends Component {
    constructor(props){
        super(props)
        this.state={
            period: 'hour',
            date: [moment(), moment()],
            refresh: moment().format('YYYY-MM-DD HH:mm:ss')
        }
    }
  componentDidMount(){
    if(this.props.slot=='today'){
      this.setState({
        date:[moment(new Date()).add(-1, 'days'),moment(new Date()).add(-1, 'days')]
      })
    }
  }
  refresh = () => {
    const {slot} = this.props
    if(slot &&slot =="today"){
      this.props.onChange([moment(new Date()).add(-1, 'days'),moment(new Date()).add(-1, 'days')])
    }else{
      this.setState({
        refresh: moment().format('YYYY-MM-DD HH:mm:ss')
      })
      this.props.onChange([moment(),moment()])
    }
  }
  onChangeType = (e)=>{
    const {value} =e
    // console.log('time', value)
    this.setState({
      mode: value == 'month' ? ['month', 'month'] : (value=='day'? ['date', 'date']:['date','date']),
      period: value
    })
     this.props.onChangeType(value)
  }
  onChange = ({componentName, value}) => {
    switch (componentName) {
      case 'SimpleDatePicker':
      case 'RangeDatePicker': {
        this.setState({
            date:value
        })
        break;
      }
      case 'PeriodSelect': {
          this.setState({
            mode: value == 'month' ? ['month', 'month'] : (value=='day'? ['date', 'date']:['date','date']),
            period: value
        })
      }
    }
    
    this.props.onChange(value)
  }

  render () {
    const { title, simpleDate, RangeDate, Period } = this.props
    const {date, refresh, mode, period} = this.state
    return (
      <div className={styles.search}>
        <div className='title-part'>
          <div className='left-part'>
            <div className='title'>{title}</div>
          </div>
          <div className='right-part'>
            <Icon type='sync' style={{color: '#b6e785', cursor: 'pointer'}} onClick={this.refresh}/>&nbsp;&nbsp;
            同步于 {refresh}
          </div>
        </div>
        <div className='time-search'>
          <Form layout='inline'>
            {simpleDate && <FormItem>
              <SimpleDatePicker date={date} onChange={this.onChange} Period={Period} currentPeriod={period} slot={this.props.slot}  disabled={period === 'month'} />
            </FormItem>}
            {RangeDate && <FormItem>
              <RangeDatePicker date={date}  onChange={this.onChange} Period={Period} mode={mode} currentPeriod={period}/>
            </FormItem>}
            {Period && <FormItem>
              <PeriodSelect date={date} onChange={this.onChangeType} mode={mode} defaultPeriod={'hour'}/>
            </FormItem>}
          </Form>
        </div>
      </div>

    )
  }
}

