// import { login } from '../../services/login'
import * as apis from '../../../services/page'
// import { message } from 'antd'
import moment from 'moment'

export default {
  state: {
    loading: false,
    period: 'hour',
    mode: ['date', 'date'],
    total: 0,
    data:[],
    date:[moment().format('YYYY-MM-DD'),moment().format('YYYY-MM-DD')],
    length:60,
    page:1,
    sort:'pv',
    order:'descend'
  },
  reducers: {
    setPageLoading (state, payload) {
      state.loading = payload
      return state
    },
    setData(state,payload){
      state.data = payload.visitors||[]
      state.total = payload.total || 0
    },
    setState(state,payload){
      for(let i in payload){
        state[i]=payload[i]
      }
    },
  },
  effects: dispatch => ({
    async seatch (payload,state) {
      this.setPageLoading(true)
      const {page, length, order, sort,date} = payload
      if(page){
        this.setState({page})
      }
      if(length){
        this.setState({length})
      }
      if(sort){
        this.setState({sort})
      }
      if(order){
        this.setState({order})
      }
      if(date){
        this.setState({date})
      }
      const data = await apis.useList({
        page:page,
        order:order?order:state.user.order,
        length:length?length:state.user.length,
        sort:sort?sort:state.user.sort,
        date:date?date:state.user.date,
      })
      this.setData(data)
      this.setPageLoading(false)
    },
  })
}
