import request from '../../../service'

export async function list (params) {
  return request('/page', {
    method: 'get',
    data: params
  })
}

export async function visitSummary (params) {
  return request('/page/summary', {
    method: 'get',
    data: params
  })
}
export async function visitTrend (params) {
  return request('/page/trend', {
    method: 'get',
    data: params
  })
}